import React, { useContext, useEffect, useState } from 'react';
import { AppContext } from "../../../context/appContext";
import axios from "axios";
import { ServerUrl } from "../../../config";
import TeamRow from './teamRow';
import Loading from '../../../components/common/loading';
import CreateNewButton from '../../../components/common/buttons/createNewButton';
import { Collapse } from '@mui/material';
import CreateNewTeam from './createNewTeam';
import { useNavigate } from 'react-router-dom';
import { StyledBackButton, StyledPageTitle, StyledPageContainer, StyledTableContainer, StyledTableHeader } from '../../../components/styled';
import WithPosition from '../../../components/common/hoc/resetPagePosition';

const Teams = () => {
  const { CurrentUser } = useContext(AppContext);
  const [teams, setTeams] = useState([]);
  const [loading, setLoading] = useState(false);
  const [showCreate, setShowCreate] = useState(false);
  const navigate = useNavigate();

  const LoadTeams = () => {
    setLoading(true);
    axios.get(`${ServerUrl}/api/teams`, 
      { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }})
    .then(res => {
      setTeams(res.data);
    }).finally(() => setTimeout(() => setLoading(false), 1000));    
  }

  useEffect(() => {
    window.scrollTo(0, 0);
    LoadTeams();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const onCreate = (data) => {
    axios.post(`${ServerUrl}/api/teams`, data, { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }})
      .then(() => { setShowCreate(false); LoadTeams()})
  }

  return (
    <StyledPageContainer>
      <div style={{display: 'flex', justifyContent: 'space-between', padding: '20px 20px 10px 20px', alignItems: 'center'}}>
        <div style={{display: 'flex', gap:"40px", alignItems: 'center'}}>
          <StyledPageTitle>My Teams</StyledPageTitle>
          <StyledBackButton onClick={() => navigate("/yourHome")}>Back to My Home</StyledBackButton>
        </div>
        <CreateNewButton title="New Team" onClick={() => setShowCreate(prev => !prev)} showCreate={showCreate} />
      </div>
      <Collapse in={showCreate}>
        <CreateNewTeam onCreate={onCreate} />
      </Collapse>
      {loading && <Loading />}
      {!loading && teams.length > 0 &&
        <>
          <div style={{padding: '10px 20px 20px 20px'}}>
            <StyledTableContainer>
              <StyledTableHeader>
                <span>Name</span> 
              </StyledTableHeader>
              <StyledTableHeader>
                <span>Description</span> 
              </StyledTableHeader>

              <div style={{width: '180px'}}>
              </div>
            </StyledTableContainer>

            {teams.map((teamItem, index) => 
              <TeamRow key={index} item={teamItem} onTaskDone={() => LoadTeams()} />
            )}
          </div>
        </>
      }
      {!loading && teams.length === 0 &&
        <div style={{display:'flex', justifyContent:'center', fontSize: '18px'}}>
          No Team found
        </div>
      }
    </StyledPageContainer>
  )
};

export default WithPosition(Teams);