import React, { useState, useContext, useEffect } from "react";
import Button from '../../components/common/buttons/IconRatationButton';
import Card from "./card";
import axios from 'axios';
import { AppContext } from '../../context/appContext';
import { ServerUrl } from '../../config';
import { TextField, InputAdornment, IconButton } from "@mui/material";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";

const PasswordReminder = "Password must contain one uppercase and one lowercase character and one digit and length between 6 and 32.";

const ChangePassword = () => {
  const [currentPassword, setCurrentPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [showProgress, setShowProgress] = useState(false);
  const [newPasswordError, setNewPasswordError] = useState(false);
  const [confirmPasswordError, setConfirmPasswordError] = useState(false);
  const [currentPasswordError, setCurrentPasswordError] = useState(false);
  const [showCurrentPassword, setShowCurrentPassword] = useState(false);
  const [showNewPassword, setShowNewPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);

  const { CurrentUser, dispatch } = useContext(AppContext);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const ValidatePassword = (s) => {
    return /^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z]).{6,32}$/.test(s);
  }

  const onChangePassword = () => {
    var currentError = false;
    if (!ValidatePassword(currentPassword)) {
      currentError = true;
      setCurrentPasswordError(true);
    }
    var newPassError = false;
    if (!ValidatePassword(newPassword)) {
      newPassError = true;
      setNewPasswordError(true);
    }

    var confirmError = false;
    if (confirmPassword !== newPassword) {
      confirmError = true;
      setConfirmPasswordError(true);
    }

    if (currentError || newPassError || confirmError) return;

    setShowProgress(true);
    axios.post(`${ServerUrl}/api/Auth/changepassword`, {currentPassword, newPassword},
      { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }}
    ).then(res => {
      dispatch({type:'SET_MESSAGE', data: {title: 'Success', lines: ["Your password has been changed successfully."]}})
    }).catch((err) => {
      dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: [err.response.data]}})
    }).finally(() => {
      setShowProgress(false);
    });
  }

    return (
      <div style={{minHeight: '600px'}}>
      <Card title="Change Password">
        <div style={{fontSize: '18px'}}>Current Password</div>
        <TextField style={{ width: '100%'}}
            size="small"
            margin="dense"
            type={showCurrentPassword ? "text" : "password"}
            fullWidth
            variant="outlined"
            value={currentPassword}
            onChange={(e) => {setCurrentPassword(e.target.value); setCurrentPasswordError(false)}}
            InputProps={{ 
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={() => setShowCurrentPassword(!showCurrentPassword)}
                  >
                    {showCurrentPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              )
            }}
        />
        {currentPasswordError && <div style={{fontSize: '14px', color: 'red'}}>Invalid current password</div>}

        <div style={{fontSize: '18px', marginTop: '20px'}}>New Password</div>
        <TextField  style={{ width: '100%'}}
            size="small"
            margin="dense"
            type={showNewPassword ? "text" : "password"}
            fullWidth
            variant="outlined"
            value={newPassword}
            onChange={(e) => {setNewPassword(e.target.value); setNewPasswordError(false)}}
            InputProps={{ 
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={() => setShowNewPassword(!showNewPassword)}
                  >
                    {showNewPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              )
            }}
        />
        {newPasswordError && <div style={{fontSize: '14px', color: 'red'}}>{PasswordReminder}</div>}

        <div style={{fontSize: '18px', marginTop: '20px'}}>Confirm Password</div>
        <TextField  style={{ width: '100%'}}
            size="small"
            margin="dense"
            type={showConfirmPassword ? "text" : "password"}
            fullWidth
            variant="outlined"
            value={confirmPassword}
            onChange={(e) => { setConfirmPassword(e.target.value); setConfirmPasswordError(false)}}
            InputProps={{ 
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                  >
                    {showConfirmPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              )
            }}
        />
        {confirmPasswordError && <div style={{fontSize: '14px', color: 'red'}}>Password not match</div>}

        <div style={{display:'flex', justifyContent:'center', margin:'20px 10px 20px 10px'}}>
          <Button showIcon={showProgress} label="Change Password" onClick={onChangePassword} 
            style={{color: 'white', backgroundColor:'#1976D3', width: '220px', padding: '12px 20px', margin: 'auto'}} />
        </div>

      </Card>
      </div>
    );
}

export default ChangePassword;

