import React, { useState, useContext, useEffect } from "react";
import Button from '../../components/common/buttons/IconRatationButton';
import Card from "./card";
import axios from 'axios';
import { AppContext } from '../../context/appContext';
import { useSearchParams } from 'react-router-dom';
import { ServerUrl } from '../../config';
import { TextField, InputAdornment, IconButton } from "@mui/material";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";

const ResetPassword = () => {
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [showProgress, setShowProgress] = useState(false);
  const [passwordError, setPasswordError] = useState(false);
  const [confirmPasswordError, setConfirmPasswordError] = useState(false);
  const [resetEmail, setResetEmail] = useState('');
  const [resetCode, setResetCode] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);

  let [searchParams] = useSearchParams();
  let paraEmail = searchParams.get("email");
  let paraCode = searchParams.get("code");
 
  const { dispatch } = useContext(AppContext);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  
  useEffect(() => {
    if (paraEmail && paraCode) {
      setResetEmail(paraEmail);
      setResetCode(paraCode);  
    }
  },[paraEmail, paraCode]);

  const ValidateEmail = (email) => {
    return /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(email);
  }

  const onResetPassword = () => {
    if (!ValidateEmail(resetEmail)) {
      return;
    }
    var passError = false;
    if (!ValidatePassword(password)) {
      passError = true;
      setPasswordError(true);
    }

    var confirmPassError = false;
    if (confirmPassword !== password) {
      confirmPassError = true;
      setConfirmPasswordError(true);
    }

    if (passError || confirmPassError) return;

    setShowProgress(true);
    axios.post(`${ServerUrl}/api/Auth/resetpassword`, {username: resetEmail, password, resetCode}, {
      'Content-Type': 'application/json;charset=UTF-8',
    }).then(res => {
      dispatch({type:'SET_MESSAGE', data: {title: 'Success', lines: ["You have changed password successfully."]}})
    }).catch((err) => {
      dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: [err.response.data]}})
    }).finally(() => {
      setShowProgress(false);
    });
  }

  const IsDataReady = () => {
    return ValidateEmail(resetEmail) && resetCode.length > 0;
  }

  const ValidatePassword = (s) => {
    return /^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z]).{6,32}$/.test(s);
  }
  
  return (
    <div style={{minHeight: '600px'}}>
    <Card title="Reset Password">
      <div style={{fontSize: '18px', marginTop: '20px'}}>Password</div>
      <TextField  style={{ width: '100%'}}
          size="small"
          margin="dense"
          type={showPassword ? "text" : "password"}
          fullWidth
          variant="outlined"
          value={password}
          onChange={(e) => {setPassword(e.target.value); setPasswordError(false)}}
          InputProps={{ 
            endAdornment: (
              <InputAdornment position="end">
                <IconButton
                  aria-label="toggle password visibility"
                  onClick={() => setShowPassword(!showPassword)}
                >
                  {showPassword ? <Visibility /> : <VisibilityOff />}
                </IconButton>
              </InputAdornment>
            )
          }}
      />
      {passwordError && <div style={{fontSize: '14px', color: 'red'}}>Password must have at leat one character and one number and length between 6 and 32.</div>}

      <div style={{fontSize: '18px', marginTop: '20px'}}>Confirm Password</div>
      <TextField  style={{ width: '100%'}}
          size="small"
          margin="dense"
          type={showConfirmPassword ? "text" : "password"}
          fullWidth
          variant="outlined"
          value={confirmPassword}
          onChange={(e) => { setConfirmPassword(e.target.value); setConfirmPasswordError(false)}}
          InputProps={{ 
            endAdornment: (
              <InputAdornment position="end">
                <IconButton
                  aria-label="toggle password visibility"
                  onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                >
                  {showConfirmPassword ? <Visibility /> : <VisibilityOff />}
                </IconButton>
              </InputAdornment>
            )
          }}
      />
      {confirmPasswordError && <div style={{fontSize: '14px', color: 'red'}}>Password not match</div>}

      <div style={{display:'flex', justifyContent:'center', margin:'20px 10px 20px 10px'}}>
        {IsDataReady() && <Button showIcon={showProgress} label="Reset Password" onClick={onResetPassword} 
          style={{color: 'white', backgroundColor:'#1976D3', width: '200px', padding: '12px 20px', margin: 'auto'}} />}
        {!IsDataReady() && <Button label="Reset Password" 
          style={{color: 'white', backgroundColor:'gray', width: '200px', padding: '12px 20px', margin: 'auto', cursor: "not-allowed"}} />}
      </div>

    </Card>
    </div>
  );
}

export default ResetPassword;

