import React, { useEffect, useState, useContext } from 'react';
import axios from "axios";
import { ServerUrl } from "../../../../config";
import { AppContext } from "../../../../context/appContext";
import { useParams, useNavigate } from 'react-router-dom';
import Loading from '../../../../components/common/loading';
import UserRow from './userRow';
import CreateNewButton from '../../../../components/common/buttons/createNewButton';
import { Collapse } from '@mui/material';
import CreateNewUser from './createNewUser';
import { StyledPageTitle, StyledBackButton, StyledTableContainer, StyledTableHeader } from '../../../../components/styled';
import WithPosition from '../../../../components/common/hoc/resetPagePosition';

const TeamManage = () => {
  const { id } = useParams();

  const [userData, setUserData] = useState(null);
  const [loading, setLoading] = useState(false);
  const { CurrentUser, dispatch } = useContext(AppContext);
  const { token } = CurrentUser;
  const [showCreate, setShowCreate] = useState(false);
  const [readOnly, setReadOnly] = useState(true);
  const navigate = useNavigate();

  const LoadTeams = () => {
    setLoading(true);
    axios.get(`${ServerUrl}/api/teams/${id}/users`, 
      { headers: { 'Authorization' : 'Bearer ' + token }})
    .then(res => {
      setUserData(res.data);
      setReadOnly(res.data.role !== "admin");
    }).catch(() => alert('error')).finally(() => setTimeout(() => setLoading(false), 1000));    
  };

  useEffect(() => {
    LoadTeams();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const onCreate = (email) => {
    if (readOnly) return;

    axios.post(`${ServerUrl}/api/teams/users/`, {teamId: parseInt(id), username: email}, 
      { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }})
    .then(res => {
      setShowCreate(false);
      LoadTeams();
    }).catch(() => dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: ['Fail to add user.']}}));   

  }

  return (
    <div style={{minHeight: '800px', width: '80%', margin: 'auto'}}>
      <div style={{display: 'flex', justifyContent: 'space-between', padding: '20px 20px 0px 20px'}}>
        <div style={{display: 'flex', gap:"40px", alignItems: 'center'}}>
          <StyledPageTitle>Team members</StyledPageTitle>
          <StyledBackButton onClick={() => navigate(`/teams`)}>Back to Teams</StyledBackButton>
        </div>
        {!readOnly && <CreateNewButton title="New Member" onClick={() => setShowCreate(prev => !prev)} showCreate={showCreate} disabled={readOnly} />}
      </div>
      <Collapse in={showCreate}>
        <CreateNewUser teamId={id} onCreate={onCreate} />
      </Collapse>
      {loading && <Loading />}
      {!loading && userData && userData.users.length > 0 &&
        <>
          <div style={{padding: '20px'}}>
            <StyledTableContainer>
              <StyledTableHeader>
                <span>Name</span>
              </StyledTableHeader>
              <StyledTableHeader>
                <span>Administrator</span>
              </StyledTableHeader>
              <StyledTableHeader>
                <span>Writable</span>
              </StyledTableHeader>
              <div style={{display: 'flex', width: '100px'}}>
              </div>
            </StyledTableContainer>
            {userData.users.map((user, index) => 
              <UserRow key={index} user={user} teamId={id} readOnly={readOnly} reload={() => LoadTeams()} />
            )}
          </div>
        </>
      }
      {!loading && userData &&  userData.users.length === 0 &&
        <div style={{display:'flex', justifyContent:'center', fontSize: '18px'}}>
          No member found
        </div>
      }
    </div>
  )
}

export default WithPosition(TeamManage);
