import React, { useState } from 'react';
import PropTypes from 'prop-types';
import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';

const formatAttributes = (text) => {
var result = {};
var data = text.split('="');
var items = [];

data.forEach(item => {
    var tt = item.split(`" `);
    tt.forEach(t => {
    if (t[t.length - 1] === '"') {
        items.push(t.substring(0, t.length - 1));
    } else {
        items.push(t);        
    }
    })    
})

if ((items.length % 2) === 0) {
    for (let j = 0; j < data.length; j += 2) {
    result[items[j]] = items[j + 1];
    }
}

return result;
}
  
const AddDialog = ({node, onClose, onAdd}) => {
    const [name, setName] = useState('');
    const [element, setElement] = useState('');
    const [styleText, setStyleText] = useState('');
    const [attributes, setAttributes] = useState('');
    const [content, setContent] = useState('');
    const [css, setCSS] = useState('');
    
    const onAddElement = () => {

        onAdd({
            name: name.replaceAll("\n", " ").replaceAll("\r", " ").replaceAll("\t", " ").trim(), 
            element: element.replaceAll("\n", " ").replaceAll("\r", " ").replaceAll("\t", " ").trim(), 
            style: formatKeyValue(styleText.replaceAll("\n", " ").replaceAll("\r", " ").replaceAll("\t", " ").trim()), 
            attributes: formatAttributes(attributes), 
            css: css.replaceAll("\n", " ").replaceAll("\r", " ").replaceAll("\t", " ").trim(), 
            content: content.replaceAll("\n", " ").replaceAll("\r", " ").replaceAll("\t", " ").trim()});
        setName('');
        setElement('');
        setStyleText('');
        setAttributes('');
        setContent('');
        setCSS('');
    }

    const formatKeyValue = (text) => {
        var result = {};
        var items = text.split(";");
        if (items.length > 0) {
            for (var i = 0; i < items.length; i++) {
                var keyValue = items[i].split(":");
                if (keyValue.length === 2 && keyValue[0].trim().length > 0 && keyValue[1].trim().length > 0) {
                    result[keyValue[0].trim()] = keyValue[1].trim();
                }
            }
        }
        return result;
    }

    const onCloseDlg = () => {
        onClose();
    }

    if (!node) {
        return null;
    }

    const isDataValid = () => {
        return element.replaceAll("\n", " ").replaceAll("\r", " ").replaceAll("\t", " ").trim().length > 0;
    }

    return (
        <Dialog maxWidth={'lg'} fullWidth={true} open={node !== null}>
            <DialogTitle>
                Add new node
            </DialogTitle>
            <DialogContent dividers>
                <div style={{display: 'flex', gap: '10px'}}>
                    <TextField  style={{ flex: '1 1'}}
                        multiline
                        rows={1}
                        margin="dense"
                        id="name"
                        label="Name"
                        type="text"
                        fullWidth
                        variant="outlined"
                        value={name}
                        size="small"
                        InputLabelProps={{
                            shrink: true,
                          }}
                        onChange={(e) => setName(e.target.value.trim())}
                    />
                    <TextField  style={{ flex: '1 1'}}
                        multiline
                        rows={1}
                        margin="dense"
                        id="element"
                        label="Element"
                        type="text"
                        fullWidth
                        variant="outlined"
                        value={element}
                        size="small"
                        InputLabelProps={{
                            shrink: true,
                          }}
                        onChange={(e) => setElement(e.target.value.trim())}
                    />
                </div>

                <div style={{display: 'flex', gap: '10px'}}>
                    <TextField  style={{ flex: '1 1', marginBottom: '16px'}}
                        multiline
                        rows={3}
                        margin="dense"
                        id="style"
                        label="Style"
                        type="text"
                        fullWidth
                        variant="outlined"
                        value={styleText}
                        size="small"
                        InputLabelProps={{
                            shrink: true,
                          }}
                        onChange={(e) => setStyleText(e.target.value)}
                    />
                    <TextField  style={{ flex: '1 1', marginBottom: '16px'}}
                        multiline
                        rows={3}
                        margin="dense"
                        id="attributes"
                        label="Attributes"
                        type="text"
                        fullWidth
                        variant="outlined"
                        value={attributes}
                        size="small"
                        InputLabelProps={{
                            shrink: true,
                          }}
                        onChange={(e) => setAttributes(e.target.value)}
                    />
                </div>

                <div style={{display: 'flex', gap: '10px'}}>
                    <TextField
                        multiline
                        rows={3}
                        margin="dense"
                        id="css"
                        label="CSS"
                        type="text"
                        fullWidth
                        variant="outlined"
                        value={css}
                        size="small"
                        InputLabelProps={{
                            shrink: true,
                          }}
                        onChange={(e) => setCSS(e.target.value)}
                    />
                                        
                    <TextField
                        multiline
                        rows={3}
                        margin="dense"
                        id="content"
                        label="Content"
                        type="text"
                        fullWidth
                        variant="outlined"
                        value={content}
                        size="small"
                        InputLabelProps={{
                            shrink: true,
                          }}
                        onChange={(e) => setContent(e.target.value)}
                    />
                </div>

            </DialogContent>
            <DialogActions>
                <Button onClick={onCloseDlg}>Cancel</Button>
                {isDataValid() && <Button onClick={onAddElement}>Add</Button>}
                {!isDataValid() && <Button disabled>Add</Button>}
            </DialogActions>
        </Dialog>
    )
}

AddDialog.propTypes = {
    node: PropTypes.shape(),
    onClose: PropTypes.func,
    onAdd: PropTypes.func
  };
  
export default AddDialog;
