import React, { useContext, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { ServerUrl } from '../../../../config';
import { AppContext } from '../../../../context/appContext';
import axios from 'axios';
import PageEditor from '../../pageEditor';
import TextField from '@mui/material/TextField';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import SaveIcon from '@mui/icons-material/Save';
import { StyledButton, StyledFlexContainer } from '../../../../components/styled';
import Checkbox from '@mui/material/Checkbox';
import WithPosition from '../../../../components/common/hoc/resetPagePosition';
import { getHTML } from '../../pageEditor/helper/htmlHelper';

const WebContent = () => {
  const { teamId, pageId } = useParams();
  const { CurrentUser, dispatch } = useContext(AppContext);

  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const [groupId, setGroupId] = useState(-1);
  const [groups, setGroups] = useState([]);
  const [isPublic, setIsPublic] = useState(false);
  const [dataId, setDataId] = useState(-1);
  const [treeData, setTreeData] = useState(null);

  const LoadWebpage = () => {
    if (CurrentUser && CurrentUser.token) {
      axios.get(`${ServerUrl}/api/teamcontent/${teamId}/${pageId}`, { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }})
      .then(res => {
        setName(res.data.name);
        setDescription(res.data.description);
        setIsPublic(res.data.isPublic);
        setDataId(res.data.dataId);
        setGroupId(res.data.groupId);
        setGroups(res.data.groups)
        setTreeData(JSON.parse(res.data.content));
      })
    }
  }

  useEffect(() => {
    LoadWebpage();
    // eslint-disable-next-line
  }, []);

  const SavePage = () => {
    axios.put(`${ServerUrl}/api/teamcontent/${teamId}/${pageId}`, {name, description, isPublic, groupId, content: JSON.stringify(treeData), html: getHTML(treeData)}, 
      { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }, 'Content-Type': 'application/json;charset=UTF-8'})
    .then(res => {
      dispatch({type:'SET_MESSAGE', data: {title: 'Success', lines: ['Web content is saved successfully']}})
    }).catch(() => {
      dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: ['Fail to save webpage']}})
    });
  }  

  const onDataChanged = (data) => {
    setTreeData(JSON.parse(JSON.stringify(data)));
  };

  const SwitchPublic =() => {
    var newPublic = !isPublic;
    setIsPublic(newPublic);
    if (newPublic) {
      dispatch({type:'SET_MESSAGE', data: {title: 'Confirmation', lines: ['Web content will be public after saved.', `Here is the link: https://www.w3element.com/public-contents/${dataId}`]}})
    } 
  }

  return (
    <div style={{width: '100%', height: 'calc(100vh - 60px)'}}>
      <div style={{borderBottom: '2px solid #e2e5e8', padding: '0px 10px'}}>
        <StyledFlexContainer width="1024px" style={{gap: '10px'}}>
          <StyledFlexContainer style={{flex: '2', gap: '10px'}}>
            <StyledFlexContainer style={{gap: '10px'}}>
              <div>Name</div>
              <TextField
                    size="small"
                    margin="dense"
                    type="text"
                    fullWidth
                    variant="outlined"
                    value={name}
                    onChange={(e) => { setName(e.target.value); }}
              />
            </StyledFlexContainer>
            <StyledFlexContainer style={{gap: '10px'}}>
              <div>Group</div>
              <Select size="small"
                  labelId="demo-select-small"
                  id="demo-select-small"
                  value={groupId}
                  onChange={(e) => { setGroupId(e.target.value); }}
                  style={{width: '100%'}}
                  displayEmpty
                  inputProps={{ 'aria-label': 'Without label' }}
                  >
                  {groups.map((item) => {
                    return <MenuItem key={item.id} value={item.id}>{item.name}</MenuItem>
                  })}
              </Select>
            </StyledFlexContainer>
          </StyledFlexContainer>
          <StyledFlexContainer style={{flex: '3', gap: '10px'}}>
            <div style={{flex: '1', display: 'flex', alignItems: 'center', gap: '10px'}}>
              <div>Description</div>
              <TextField  style={{ width: '100%'}}
                size="small"
                margin="dense"
                type="text"
                fullWidth
                variant="outlined"
                value={description}
                onChange={(e) => { setDescription(e.target.value); }}
              />
            </div>
            <div>
              <span style={{marginRight: '0px'}}>Public</span>
              <Checkbox checked={isPublic} onChange={SwitchPublic} inputProps={{ 'aria-label': 'controlled' }} />
            </div>
            <StyledButton backColor={'#04AA6D'} 
              backOverColor={'#059862'} 
              style={{marginRight: '0px', alignItems: 'center', cursor: "pointer"}} onClick={SavePage} >
                <SaveIcon style={{width: '20px', height: '20px', color: 'white', marginRight: '10px'}} />
                <span>Save</span>
            </StyledButton>
          </StyledFlexContainer>
        </StyledFlexContainer>
      </div>    
      {treeData && 
        <PageEditor content={treeData} onDataChanged={onDataChanged}/>
      }
    </div>
  )
}

export default WithPosition(WebContent);
