import React, { useState, useContext, useEffect } from 'react';
import TextField from '@mui/material/TextField';
import EditIcon from '@mui/icons-material/Edit';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import { AppContext } from '../../../../context/appContext';
import axios from 'axios';
import { ServerUrl } from '../../../../config';
import { StyledButton, StyledCard, StyledAddButton } from '../../../../components/styled';
import DialogContainer from '../../../../components/dialog/dialogContainer';
import { useNavigate } from 'react-router-dom';
import { getHTML } from '../../pageEditor/helper/htmlHelper';

const newTreeData = 
{
  "id": 0,
  "name": "Root",
  "element": "div",
  "isRoot": true,
  "isExpanded": true,
  "isChecked": true,
  "children": [
    {
      "name": "header",
      "element": "div",
      "style": {
        "border": "1px solid gray",
        "height": "200px",
        "display": "flex",
        "justify-content": "center",
        "align-items": "center"
      },
      "attributes": "",
      "content": "",
      "id": 1,
      "isExpanded": true,
      "isChecked": true,
      "children": [
        {
          "name": "title line",
          "element": "div",
          "style": {
            "display": "inline",
            "font-size": "24px"
          },
          "attributes": "",
          "content": "",
          "id": 2,
          "isExpanded": true,
          "isChecked": true,
          "children": [
            {
              "name": "text1",
              "element": "span",
              "style": {},
              "attributes": "",
              "content": "Please right click ",
              "id": 3,
              "isExpanded": true,
              "isChecked": true,
              "children": []
            },
            {
              "name": "text2",
              "element": "span",
              "style": {
                "font-weight": "bold"
              },
              "attributes": "",
              "content": "Root",
              "id": 4,
              "isExpanded": true,
              "isChecked": true,
              "children": []
            },
            {
              "name": "text3",
              "element": "span",
              "style": {},
              "attributes": "",
              "content": " node to add your content",
              "id": 5,
              "isExpanded": true,
              "isChecked": true,
              "children": []
            }
          ]
        }
      ]
    }
  ],
  "style": {},
  "attributes": "",
  "content": ""
};

const CreateNewPage =({onCreated, teamId}) => {
  const navigate = useNavigate();
  const { CurrentUser } = useContext(AppContext);

  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const [message, setMessage] = useState(null);
  const [errors, setErrors] = useState([]);
  const [groupId, setGroupId] = useState(-1);
  const [groups, setGroups] = useState([]);

  // eslint-disable-next-line
  useEffect(() => LoadGroup(), []);

  const LoadGroup = () => {
    if (CurrentUser && CurrentUser.token) {
      axios.get(`${ServerUrl}/api/TeamContentGroup/${teamId}`, { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }})
      .then(res => {
        setGroups(res.data);
      })
    }
  }

  const onAddPage = () => {
    if (!name || name.length === 0) {
      return;
    }

    var errors = [];
    if (groupId === -1) {
      errors.push("Please select a group")
    }

    if (errors.length > 0) {
      setErrors(errors);
      setMessage({title: 'Error'})
    } else {
      doAddPage();
    }
  }

  const doAddPage = () => {
    axios.post(`${ServerUrl}/api/TeamContent`, {name, description, teamId: parseInt(teamId), groupId, content: JSON.stringify(newTreeData), html: getHTML(newTreeData)}, 
      { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }, 'Content-Type': 'application/json;charset=UTF-8'})
    .then(res => {
      navigate(`/teams/${teamId}/webcontents/${res.data.id}`)
    }).catch(() => {
      setErrors(['Fail to create webpage']);
      setMessage({title: 'Error'})
    }).finally(() => {

    });
  }  

  return (
    <StyledCard>
        <div style={{fontSize: '18px'}}>Name</div>
        <TextField style={{ width: '100%', marginBottom: '16px'}}
              size="small"
              margin="dense"
              type="text"
              fullWidth
              variant="outlined"
              value={name}
              onChange={(e) => setName(e.target.value)}
        />
        <div style={{fontSize: '18px'}}>Description</div>
        <TextField  style={{ width: '100%', marginBottom: '16px'}}
            size="small"
            margin="dense"
            fullWidth
            variant="outlined"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
        />
        <div style={{fontSize: '18px', marginBottom: '10px'}}>Group</div>

        <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center'}}>
            <div>
              <div style={{display: 'flex', alignItems: 'center'}}>
                <div>
                  <Select
                      labelId="demo-select-small"
                      id="demo-select-small"
                      value={groupId}
                      onChange={(e) => setGroupId(e.target.value)}
                      style={{width: '200px'}}
                      displayEmpty
                      inputProps={{ 'aria-label': 'Without label' }}
                      size="small"
                      >
                      {groups.map((item) => {
                        return <MenuItem key={item.id} value={item.id}>{item.name}</MenuItem>
                      })}
                  </Select>
                </div>
                <div style={{marginLeft: '20px'}}>
                    <StyledButton style={{marginLeft: '10px', paddingLeft: '20px', paddingRight: '20px', backgroundColor: '#1976d2'}} 
                        onClick={() => navigate(`/teams/${teamId}/webcontents/group`)}>
                      <EditIcon style={{width: '20px', height: '20px', marginRight: '10px'}} /> 
                      <span>Edit Group</span>
                    </StyledButton>
                </div>

              </div>
            </div>
            <StyledAddButton disabled={name.length === 0} onClick={onAddPage}>
              <span>Add</span>
            </StyledAddButton>
        </div>

        <DialogContainer message={message} onClose={() => { setMessage(null); setErrors([]) }} >
          {errors.map((text, index) => {
            return <div key={index}>{text}</div>
          } )}
        </DialogContainer>

    </StyledCard>
  )
}

export default CreateNewPage;
