import React, { useEffect, useState } from "react";
import "react-reflex/styles.css";
import {
  ReflexContainer,
  ReflexElement,
  ReflexSplitter,
} from "react-reflex";
import styled from 'styled-components';
import Tree from './tree/tree';
import NodeEditor from "./nodeEditor/index";
import HtmlRender from "./htmlRender/index";
import HtmlPreviewer from "./htmlPreviewer/index";
import ReactRender from "./reactRender/index";
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';

export const StyledIcon = styled.div`
  display: inline-block;
  padding-top: 4px;
  margin-right: 4px;
  transition-duration: 0.5s;
  transform-origin: center center;
  transform: ${props => props.rotate ? "rotate(0deg)" : "rotate(180deg)"};
  cursor: pointer;

  &:hover {
    background-color: white;
  }
`;

const PageEditor = ({content, onDataChanged}) => {
  const [viewMode, setViewMode] = useState('preview');
  const [renderData, setRenderData] = useState(null);
  const [treeData, setTreeData] = useState(content);
  const [selectedNode, setSelectedNode] = useState(null);
  const [refresh, setRefresh] = useState(false);
  const [jsonText, setJsonText] = useState(null);

  useEffect(() => {
    if (treeData != null) {
      var newData = JSON.parse(JSON.stringify(treeData));
      generateJSON(newData);
      if (viewMode === 'json') {
        setJsonText(JSON.stringify(newData, null, 2))
      }
      setRenderData(newData);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [viewMode, treeData, refresh]);

  const generateJSON = (data) => {
    // delete data.id;
    // delete data.isRoot;
    delete data.isChecked;
    delete data.isExpanded;
    delete data.userData;

    data.children = data.children.filter(item => item.isChecked === true)

    for (var i = 0; i < data.children.length; i++) {
      generateJSON(data.children[i])
    }
  }

  const doDataChanged = (data) => {
    setTreeData(data);
    onDataChanged(data);
  }; 

  const onNodeSelected = (node) => {
    setSelectedNode(node);
  }

  const onUpdateNode = () => {
    setRefresh(!refresh);
  }

  return (
    <div style={{height: 'calc(100vh - 110px)'}}>
    <ReflexContainer orientation="vertical">
      <ReflexElement style={{height: '100%'}} className="left-pane" minSize={0} size={300}>
        <div style={{paddingBottom: '300px'}}>
          {treeData && <Tree data={treeData} onDataChanged={doDataChanged} onNodeSelected={onNodeSelected} />}
        </div>
      </ReflexElement>

      <ReflexSplitter
        style={{
          backgroundColor: "lightblue",
          textAlign: "center",
          width: "8px",
          height: "auto",
        }}
      >
      </ReflexSplitter>

      <ReflexElement className="right-pane" minSize={100}>
        <div style={{backgroundColor: '#F6F8FA', padding: '0px 0px 0px 20px', borderBottom: '2px solid #e2e5e8', display:'flex', alignItems:'center'}}>
          <RadioGroup
            row
            aria-labelledby="demo-row-radio-buttons-group-label"
            name="row-radio-buttons-group"
            value={viewMode}
            onChange={(e) => setViewMode(e.target.value)}
          >
            <FormControlLabel value="preview" control={<Radio />} label="Preview" />
            <FormControlLabel value="json" control={<Radio />} label="JSON" />
            <FormControlLabel value="html" control={<Radio />} label="HTML" />
            <FormControlLabel value="react" control={<Radio />} label="React" />
          </RadioGroup>
        </div>
        {viewMode === 'json' && <div style={{padding: '0px 20px 20px 20px'}}>
          <pre>{jsonText}</pre>
        </div>}
        {viewMode === 'preview' && (
          <div style={{paddingRight: '4px'}}>
            <HtmlPreviewer data={renderData} selectedId={selectedNode ? selectedNode.id : -1}/>
          </div>
          )
        }
        {viewMode === 'html' && (
          <div style={{padding: '10px'}}>
            <HtmlRender data={renderData} />
          </div>
          )
        }
        {viewMode === 'react' && (
          <div style={{padding: '10px'}}>
            <ReactRender data={renderData} />
          </div>
          )
        }
      </ReflexElement>

      <ReflexSplitter
        style={{
          backgroundColor: "lightblue",
          textAlign: "center",
          width: "8px",
          height: "auto",
        }}
      >
      </ReflexSplitter>

      <ReflexElement style={{height: '100%'}} className="left-pane" minSize={0} size={300}>
        <div>
          {treeData && <NodeEditor node={selectedNode} onUpdateNode={onUpdateNode}/>}
        </div>
      </ReflexElement>
    </ReflexContainer>
    </div>
  );
}

export default PageEditor;
