import React, { useState, useContext, useEffect } from "react";
import TextField from '@mui/material/TextField';
import Button from '../../components/common/buttons/IconRatationButton';
import Card from "./card";
import axios from 'axios';
import { AppContext } from '../../context/appContext';
import { ServerUrl } from '../../config';

const SignIn = () => {
  const [username, setUsername] = useState('');
  const [emailError, setEmailError] = useState(false);
  const [showProgress, setShowProgress] = useState(false);

  const { dispatch } = useContext(AppContext);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  
  const ValidateEmail = (email) => {
    return /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(email);
  }

  const onForgetPassword = () => {
    var emailError = false;
    if (!ValidateEmail(username)) {
      emailError = true;
      setEmailError(true);
    }

    if (emailError) return;

    try
    {
      setShowProgress(true);
      axios.post(`${ServerUrl}/api/Auth/forgetpassword`, {email: username}, {
        'Content-Type': 'application/json;charset=UTF-8',
      }).then(res => {
        dispatch({type:'SET_MESSAGE', data: {title: 'Success', lines: ["A reset password link have been sent to your email."]}})
      }).catch((err) => {
        dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: [err.response.data]}})
      }).finally(() => {
        setShowProgress(false);
      });
    }
    catch 
    {
      dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: ["Fail to send reset password email."]}})
    }
  }

    return (
      <div style={{minHeight: '600px'}}>
      <Card title="Forget password">
        <div style={{fontSize: '18px', marginBottom: '20px'}}>Enter your account email address to receive a verification code to reset your password.</div>
        <div style={{fontSize: '18px'}}>Email</div>
        <TextField style={{ width: '100%', marginBottom: '0px'}}
            size="small"
            margin="dense"
            type="text"
            fullWidth
            variant="outlined"
            value={username}
            onChange={(e) => {setUsername(e.target.value); setEmailError(false) }}
        />
        {emailError && <div style={{fontSize: '14px', color: 'red'}}>Invalid email</div>}
        <div style={{display:'flex', justifyContent:'center', margin:'20px 10px 20px 10px'}}>
          <Button showIcon={showProgress} label="Send verification code" onClick={onForgetPassword} 
            style={{color: 'white', backgroundColor:'#1976D3', width: '260px', padding: '12px 20px', margin: 'auto'}} />
        </div>
      </Card>
      </div>
    );
}

export default SignIn;

