import styled from 'styled-components';

export const StyleRotation = styled.div`
  display: inline-block;
  padding-top: 4px;
  margin-right: 4px;
  transition-duration: 0.5s;
  transform-origin: center center;
  transform: ${props => props.rotate ? "rotate(0deg)" : "rotate(180deg)"};
  cursor: pointer;
`;

export const StyledButton = styled.div`
 color: white;
 background-color: ${props => props.disabled ? "darkgray" : props.backColor || '#04AA6D' };
 font-size: 16px;
 border-radius: 6px;
 padding: 8px 12px;
 display: flex;
 align-items: center;
 cursor: ${props => props.disabled ? "not-allowed" : "pointer"};

 &:hover {
  background-color: ${props => props.disabled ? "darkgray" : props.backOverColor || '#059862'};
 }
`;

export const StyledAddButton = styled.div`
 color: white;
 background-color: ${props => props.disabled ? "darkgray" : props.backColor || '#04AA6D' };
 font-size: 16px;
 border-radius: 6px;
 padding: 8px 30px;
 display: flex;
 align-items: center;
 cursor: ${props => props.disabled ? "not-allowed" : "pointer"};

 &:hover {
  background-color: ${props => props.disabled ? "darkgray" : props.backOverColor || '#059862'};
 }
`;

export const StyledBackButton = styled.div`
 color: white;
 background-color: ${props => props.disabled ? "darkgray" : '#1976D2' };
 font-size: 16px;
 font-family: 'Source Sans Pro', sans-serif;
 border-radius: 6px;
 padding: 8px 12px;
 display: flex;
 align-items: center;
 cursor: ${props => props.disabled ? "not-allowed" : "pointer"};

 &:hover {
  background-color: ${props => props.disabled ? "darkgray" : '#0966E2'};
 }
`;

export const StyledIcon = styled.div`
  display: inline-block;
  padding-top: 4px;
  margin-right: 4px;
  transition-duration: 0.5s;
  transform-origin: center center;
  transform: ${props => props.isOpen ? "rotate(45deg)" : "rotate(0deg)"};
`;

export const StyledCard = styled.div`
  background-color: white;
  margin: 4px 20px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
`;

export const StyledHover = styled.div`
  &:hover {
    color: ${props => props.hoverColor || 'black'};
    background-color: ${props => props.hoverBackColor || '#e2e5e8'};
  }
`;

export const StyledBox = styled.div`
  display: flex;
  border: 1px solid #e2e5e8;
  border-radius: 6px;
  width: 160px;
  height: 160px;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

  &:hover {
    border: 3px solid #007aff;
  }
`;

export const StyledBoxContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  padding-top: 20px;
  gap: 60px;
`;

export const StyledPageTitle = styled.div`
  display: flex;
  align-items: center;
  margin-right: 30px;
  font-size: 24px;
  font-weight: bold;
  white-space: nowrap;
`;

export const StyledFindLabel = styled.div`
  margin-right: 10px;
  font-size: 18px;
  font-weight: bold;
  white-space: nowrap;
`;

export const StyledPageContainer = styled.div`
  min-height: 800px;
  width: 80%;
  margin: auto;
  font-family: san-serif, Geneva, Tahoma;
`;

export const StyledTableHeader = styled.div`
  display: flex;
  align-items: center;
  font-weight: bold;
  flex: 1;

  padding: 4px 10px 4px 0px; 
  font-size: 16px;
  color: black;
  font-weight: bold;
  cursor: pointer;
`;

export const StyledTableContainer = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 10px 5px 10px;
  border-bottom: 1px solid #dfe1e6;
  background-color: #f4f5f7;
  height: 50px;
`;

export const StyledTableRow = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid lightgray;
  padding: 10px;
  height: 30px;
  &:hover {
    background-color: #f2f5f8;
  }
`;

export const StyledTableCell = styled.div`
  font-size: 14px;
  color: black;
  flex: 1;
`;

export const StyledTableCellBold = styled.div`
  font-size: 16px;
  color: #0969da;
  font-weight: bold;
  flex: 1;
  display: flex;
  align-items: center;
`;

export const StyledFlexContainer = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  width: 100%;

  @media (max-width: ${props => props.width}) {
    flex-direction: column;
  }
`;