import React, { useState, useContext } from 'react';
import styled from 'styled-components';
import DeleteIcon from '@mui/icons-material/Delete';
import axios from 'axios';
import { AppContext } from '../../../../context/appContext';
import CheckBox from '../../../../components/common/checkBox/checkBox';
import { ServerUrl } from '../../../../config';
import OkCancelDialog from './OkCancelDialog';
import { StyledTableCellBold, StyledTableRow, StyledTableCell } from '../../../../components/styled/index';
import Tooltip from '@mui/material/Tooltip';

const StyledDiv = styled.div`
  padding: 6px;
  cursor: pointer;
  &:hover {
    background-color: white;
  }
`;

const TeamRow = ({user, teamId, readOnly, reload}) => {
  const { CurrentUser, dispatch } = useContext(AppContext);
  
  const [isOver, setIsOver] = useState(false);
  const [isAdmin, setIsAdmin] = useState(user.isAdmin);
  const [isWritable, setIsWritable] = useState(user.writable);
  const [deleteOpen, setDeleteOpen] = useState(false);

  const onAdminChange = () => {
    if (readOnly) return;

    axios.put(`${ServerUrl}/api/teams/user`, {teamId: parseInt(teamId), username: user.username, isAdmin: !isAdmin, isWritable}, 
    { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }, 'Content-Type': 'application/json;charset=UTF-8'})
    .then(res => {
      setIsAdmin(!isAdmin);
    }).catch((err) => {
      dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: [err.response.data]}})
    });    
  }

  const onWritableChange = () => {
    if (readOnly) return;

    axios.put(`${ServerUrl}/api/teams/user`, {teamId: parseInt(teamId), username: user.username, isAdmin: isAdmin, isWritable: !isWritable}, 
    { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }, 'Content-Type': 'application/json;charset=UTF-8'})
    .then(res => {
      setIsWritable(!isWritable);
    }).catch((err) => {   
      dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: [err.response.data]}})   
    });
  }

  const onDeleteUser = () => {
    if (readOnly) return;

    axios.delete(`${ServerUrl}/api/teams/${teamId}/users/${user.id}`,  
    { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }, 'Content-Type': 'application/json;charset=UTF-8'})
    .then(res => {
      setDeleteOpen(false);
      reload();
    }).catch((err) => {   
      setDeleteOpen(false)
      dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: [err.response.data]}})
    });
    
  }

  return (
    <StyledTableRow onMouseEnter={() => setIsOver(true)} onMouseLeave={() => setIsOver(false)}>
        <StyledTableCellBold>
          {user.username}
        </StyledTableCellBold>
        {readOnly ? 
          <StyledTableCell>
            {isAdmin && "Yes"}
          </StyledTableCell> :
          <StyledTableCell onClick={onAdminChange}>
            <CheckBox checked={isAdmin} />
          </StyledTableCell>
        }
        {readOnly ? 
          <StyledTableCell>
            {isWritable && "Yes"}
          </StyledTableCell> :
          <StyledTableCell onClick={onWritableChange}>
            <CheckBox checked={isWritable} />
          </StyledTableCell>
        }

        <div style={{display: 'flex', width: '60px'}}>
          {!readOnly && isOver &&
            <Tooltip title="Remove member">
              <StyledDiv style={{padding: '6px', cursor: 'pointer'}} onClick={() => setDeleteOpen(true)}>
                <DeleteIcon style={{width: '24px', height: '24px', color: '#C60001'}} />
              </StyledDiv>
            </Tooltip>
          }
        </div>

        <OkCancelDialog open={deleteOpen} 
          title="Delete confirmation"
          contents={[`Are you sure to remove member ${user.username}?`]}
          onCancel={() => setDeleteOpen(false)} 
          onOk={onDeleteUser} />
    </StyledTableRow>
  )
}

export default TeamRow;