import React, {useState, useEffect} from 'react';
import TextField from '@mui/material/TextField';
import UpdateIcon from '@mui/icons-material/Update';
import { StyledButton } from '../../../../components/styled';

const NodeEditor = ({node, onUpdateNode}) => {
  const [name, setName] = useState('');
  const [element, setElement] = useState('');
  const [styleText, setStyleText] = useState('');
  const [attributes, setAttributes] = useState('');
  const [content, setContent] = useState('');
  const [css, setCSS] = useState('');

  useEffect(() => {
      if (node) {
          setName(node.name || '');
          setElement(node.element || '');
          if (node.style) {
            var textStyle = '';
            for (const [key, value] of Object.entries(node.style)) {
              textStyle += `${key}: ${value};\r\n`;
            }              
            setStyleText(textStyle);
          }
          if (node.attributes) {
            var attributesText = '';
            for (const [key, value] of Object.entries(node.attributes)) {
              attributesText += `${key}="${value}" `;
            }              
            setAttributes(attributesText);
          }
          setContent(node.content || '');
          setCSS(node.css || '');
      }
  }, [node]);

  const onUpdate = () => {
    if (element.trim().length === 0) return;

    if (node) {
      node.name = name.trim();
      node.element = element.trim();
      node.style = formatStyle(styleText.trim());
      node.attributes = formatAttributes(attributes.trim());
      node.content = content.trim();
      node.css = css.trim();
      onUpdateNode();
    }
  }

  const formatStyle = (text) => {
    var result = {};
    var items = text.split(";");
    if (items.length > 0) {
        for (var i = 0; i < items.length; i++) {
            var keyValue = items[i].split(":");
            if (keyValue.length === 2 && keyValue[0].trim().length > 0 && keyValue[1].trim().length > 0) {
                result[keyValue[0].trim()] = keyValue[1].trim();
            }
        }
    }
    return result;
  }

  const ProcessAttributes = (text) => {
    var count = text.split('"').length - 1;

    if ((count % 2) !== 0 || count === 0) return null;

    var endIndex = 0;
    var beginIndex = 0;
    var num = 0;
    var data = [];
    while (num < count) {
        var index = text.indexOf('"', endIndex);
        endIndex = index + 1;
        
        num++;
        if ((num % 2) === 0) {
            data.push(text.substring(beginIndex, endIndex));
            beginIndex = endIndex;
        }
    }

    return data;    
  }

const formatAttributes = (text) => {
  var result = {};
  var data = text.split('="');
  var items = [];

  data.forEach(item => {
    var tt = item.split(`" `);
    tt.forEach(t => {
      if (t[t.length - 1] === '"') {
        items.push(t.substring(0, t.length - 1));
      } else {
        items.push(t);        
      }
    })    
  })
  
  if ((items.length % 2) === 0) {
    for (let j = 0; j < items.length; j += 2) {
      result[items[j]] = items[j + 1];
    }
  }
  
  return result;
}

const formatAttributes2 = (text) => {
  var result = {};
  var items = ProcessAttributes(text);
  if (items && items.length > 0) {
      for (var i = 0; i < items.length; i++) {
          var keyValue = items[i].split("=");
          if (keyValue.length === 2 && keyValue[0].trim().length > 0 && keyValue[1].trim().length > 0) {
              result[keyValue[0].trim()] = keyValue[1].trim().replaceAll("\"", "");
          }
      }
  }
  return result;
}

  // if (node === null) return null;

  return (
    <div style={{padding: '4px'}}>
      <div style={{display: 'flex', justifyContent: 'flex-start', borderBottom: '1px solid #c2c2c2', paddingBottom: '4px'}}>
        <StyledButton disabled={element.trim().length === 0} backColor={'#04AA6D'} backOverColor={'#059862'} style={{alignItems: 'center', width: '80px'}} onClick={onUpdate}>
            <UpdateIcon style={{width: '20px', height: '20px', color: 'white', marginRight: '10px'}} />
            <span>Update</span>
        </StyledButton>
      </div>
      <div style={{display: 'flex', gap: '0px', flexDirection: 'column', height: 'calc(100vh - 160px)', overflowY: 'auto'}}>
          <TextField
              rows={1}
              margin="dense"
              id="name"
              label="Name"
              type="text"
              fullWidth
              variant="outlined"
              value={name}
              size="small"
              InputLabelProps={{
                shrink: true,
              }}
              onChange={(e) => setName(e.target.value)}
          />
          {element.length > 0 && <TextField
              rows={1}
              margin="dense"
              id="element"
              label="Element"
              type="text"
              fullWidth
              variant="outlined"
              value={element}
              size="small"
              InputLabelProps={{
                shrink: true,
              }}
              onChange={(e) => setElement(e.target.value)}
          />}
          {element.length === 0 &&<TextField
              rows={1}
              margin="dense"
              id="element"
              error
              label="Element is required"
              type="text"
              fullWidth
              variant="outlined"
              value={element}
              size="small"
              InputLabelProps={{
                shrink: true,
              }}
              onChange={(e) => setElement(e.target.value)}
          />}

          <TextField
              multiline
              margin="dense"
              id="style"
              label="Style"
              type="text"
              fullWidth
              variant="outlined"
              value={styleText}
              size="small"
              InputLabelProps={{
                shrink: true,
              }}
              onChange={(e) => setStyleText(e.target.value)}
          />
          <TextField 
              multiline
              margin="dense"
              id="attributes"
              label="Attributes"
              type="text"
              fullWidth
              variant="outlined"
              value={attributes}
              size="small"
              InputLabelProps={{
                shrink: true,
              }}
              onChange={(e) => setAttributes(e.target.value)}
          />
          <TextField
              multiline
              margin="dense"
              id="content"
              label="CSS"
              type="text"
              fullWidth
              variant="outlined"
              value={css}
              size="small"
              InputLabelProps={{
                shrink: true,
              }}
              onChange={(e) => setCSS(e.target.value)}
          />
          <TextField
              multiline
              margin="dense"
              id="content"
              label="Content"
              type="text"
              fullWidth
              variant="outlined"
              value={content}
              size="small"
              InputLabelProps={{
                shrink: true,
              }}
              onChange={(e) => setContent(e.target.value)}
          />
      </div>
    </div>
  )
}

export default NodeEditor;