
import React, { useState, useEffect, useContext } from 'react';
import TreeNode from './treeNode';
import AddDialog from './dialog/addDialog';
import ImportDialog from './dialog/importDialog';
import DeleteDialog from './dialog/deleteDialog';
import { AppContext } from '../../../../context/appContext';

const reorderNode = (data) => {
  var tmp = {};
  tmp['id'] = data['id'];
  tmp['name'] = data['name'];
  tmp['element'] = data['element'];
  tmp['attributes'] = data['attributes'];
  tmp['style'] = data['style'];
  tmp['children'] = data['children'];

  delete data['id'];
  delete data['name'];
  delete data['element'];
  delete data['attributes'];
  delete data['style'];
  delete data['children'];

  data['id'] = tmp['id'];
  data['name'] = tmp['name'];
  data['element'] = tmp['element'];
  data['attributes'] = tmp['attributes'];
  data['style'] = tmp['style'];
  data['children'] = tmp['children'];
  
  for (var i = 0; i < data.children.length; i++) {
    reorderNode(data.children[i]);
  }
}

const Tree = ({data, onDataChanged, onNodeSelected}) => {
  const { dispatch } = useContext(AppContext);
  const [treeData, setTreeData] = useState(null)
  const [menuId, setMenuId] = useState(-1);

  const [addNode, setAddNode] = useState(null)
  const [importNode, setImportNode] = useState(null)
  const [deleteNode, setDeleteNode] = useState(null)
  const [copiedNode, setCopiedNode] = useState(null)
  const [selectedNode, setSelectedNode] = useState(null)
  
  useEffect(() => {
    if (data) {
      reorderNode(data);
      setTreeData(data);
      // onNodeSelected(data);
      // setSelectedNode(data);
    }
  }, [data, onNodeSelected]);

  const FindNode = (data, id) => {
    if (data.id === id) return data;

    for (var i = 0; i < data.children.length; i++) {
      var found = FindNode(data.children[i], id);
      if (found != null) return found;
    }

    return null;
  }

  const onToggleExpand = (node) => {
    var found = null;
    var newData = JSON.parse(JSON.stringify(treeData));
    found = FindNode(newData, node.id);
    if (found != null) {
      if (found.isExpanded) {
        found.isExpanded = !found.isExpanded;
      }
      else {
        found.isExpanded = true;
      }

      reorderNode(newData);
      setTreeData(newData);
      onDataChanged(newData);
    }
  };

  const onToggleChecked = (node) => {
    var hasRoot = node.hasOwnProperty("isRoot");
    if (hasRoot && node.isRoot) return;

    var found = null;
    var newData = JSON.parse(JSON.stringify(treeData));
    found = FindNode(newData, node.id);
    if (found != null) {
      if (!found.isChecked) {
        found.isChecked = true;
      }
      else {
        found.isChecked = !found.isChecked;
      }

      reorderNode(newData);
      setTreeData(newData);
      onDataChanged(newData);
    }
  };

  const onNodeClick = (action, node) => {
    if (action === "ToggleChecked") {
      onToggleChecked(node);
    } 
    else if (action === "ToggleExpand") {
      onToggleExpand(node);
    } 
    else if (action === "SelectNode") {
      onNodeSelected(node);
      setSelectedNode(node);
    }
    else if (action === "OpenMenu") {
        if (node) {
        setMenuId(node.id);
      } else {
        setMenuId(-1);
      }
    } 
    else if (action === "AddNode") {
      setAddNode(node);
    }
    else if (action === "ImportNode") {
      setImportNode(node);
    }
    else if (action === "DeleteNode") {
      setDeleteNode(node);
    }
    else if (action === "CutNode") {
      navigator.clipboard.writeText(JSON.stringify(node));
      onNodeDelete(node);
    }
    else if (action === "CopyNode") {
      let newNode = JSON.parse(JSON.stringify(node));
      delete newNode.isRoot;
      setCopiedNode(newNode);
    }
    else if (action === "PasteNode") {
      if (copiedNode) {
        const values =  ["content", "name", "element", "attributes", "style", "children"];
        var keys = Object.keys(copiedNode);
        const isValid = values.every(value => {
          return keys.includes(value);
        });

        if (!isValid) {
          dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: ['Invalid data.']}});
          return;
        }
        else {
            var result = {max: 0};
            getMaxId(treeData, result);
            updateId(copiedNode, result);
            node.children.push(JSON.parse(JSON.stringify(copiedNode)));
            let newTreeData = JSON.parse(JSON.stringify(treeData));

            reorderNode(newTreeData);
            setTreeData(newTreeData)
            onDataChanged(newTreeData);

          return;
        }
      }
    }
  }

  const onNodeDelete = (node) => {
    if (!node) return;

    let newTreeData = JSON.parse(JSON.stringify(treeData));
    removeNode(newTreeData, node.id);

    reorderNode(newTreeData);
    setTreeData(newTreeData)
    onDataChanged(newTreeData);

    setDeleteNode(null);
  }

  const onImportNode = (obj) => {
    if (!importNode) return;
    
    if ( typeof(obj) === "object") {
      const values =  ["content", "name", "element", "attributes", "style", "children"];
      var keys = Object.keys(obj);
      const isValid = values.every(value => {
        return keys.includes(value);
      });

      if (!isValid) {
        dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: ['Invalid data.']}});
        return;
      }
      else {
          var result = {max: 0};
          getMaxId(treeData, result);
          updateId(obj, result);
          importNode.children.push(JSON.parse(JSON.stringify(obj)));
          let newTreeData = JSON.parse(JSON.stringify(treeData));

          reorderNode(newTreeData);
          setTreeData(newTreeData)
          onDataChanged(newTreeData);
      }
    }    

    setImportNode(null);
  }

  const onAddNode = (obj) => {
    var result = {max: 0};
    getMaxId(treeData, result);
    addNode.children.push(Object.assign({}, obj, {id: result.max + 1,  isExpanded: true, isChecked: true, children: []}))

    let newTreeData = JSON.parse(JSON.stringify(treeData));

    reorderNode(newTreeData);
    setTreeData(newTreeData)
    onDataChanged(newTreeData);    

    setAddNode(null);
  }

  const getMaxId = (data, result) => {
    if (data.id > result.max) {
      result.max = data.id;
    }

    for (var i = 0; i < data.children.length; i++) {
      getMaxId(data.children[i], result);
    }

    result.max += 1;
  }

  const updateId = (data, maxObj) => {
    data.id = maxObj.max;
    maxObj.max += 1;

    for (var i = 0; i < data.children.length; i++) {
      updateId(data.children[i], maxObj);
    }
  }

  const removeNode = (data, id) => {
    var found = data.children.find(p => p.id === id);
    if (found) {
      data.children = data.children.filter(p => p.id !== id);
    };

    for (var i = 0; i < data.children.length; i++) {
      removeNode(data.children[i], id);
    }
  }

  const onMenuClick = (id) => {
    setMenuId(id);
  }

  const onDragDrop = (dragId, dropId) => {
    let newTreeData = JSON.parse(JSON.stringify(treeData));

    var dragObj = getChildNode(newTreeData, parseInt(dragId));
    if (dragObj) {
      var childObj = getChildNode(dragObj, parseInt(dropId));
      if (childObj) {
        dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: ['Canot drop to child node.']}});
        return;
      }

      var dropObj = getChildNode(newTreeData, parseInt(dropId));
      if (dragObj && dropObj) {
        var newObj = JSON.parse(JSON.stringify(dragObj));

        removeNode(newTreeData, parseInt(dragId));
    
        var result = {max: 0};
        getMaxId(newTreeData, result);
        updateId(newObj, result);
        dropObj.children.unshift(newObj);
        
        reorderNode(newTreeData);
        setTreeData(newTreeData)
        onDataChanged(newTreeData);
      }
    }
  }

  const getChildNode = (node, id) => {
    if (node.id === id) {
      return node;
    }

    for (var i = 0; i < node.children.length; i++) {
      var currentNode = getChildNode(node.children[i], id);
      if (currentNode) return currentNode;
    }

    return null;
  }

  return (
    <div>
      {treeData && 
         <TreeNode node={treeData} level={0} menuId={menuId} 
              onMenuClick={onMenuClick} onNodeClick={onNodeClick} 
              selectedNode={selectedNode} onDragDrop={onDragDrop} />}

      <AddDialog node={addNode} onClose={() => setAddNode(null)} onAdd={onAddNode} />
      <ImportDialog node={importNode} onClose={() => setImportNode(null)} onImportNode={onImportNode} />
      <DeleteDialog node={deleteNode} onCancel={() => setDeleteNode(null)} onOk={() => { onNodeDelete(deleteNode); }} disableScrollLock />
    </div>
  );
};

export default Tree;