import React, { useState, useEffect, useContext } from "react";
import { Collapse } from '@mui/material';
import CreateNewGroup from "./createnewgroup";
import axios from 'axios';
import { ServerUrl } from "../../../../../config";
import { AppContext } from "../../../../../context/appContext";
import GroupRow from "./grouprow";
import { StyledBackButton, StyledPageContainer, StyledPageTitle, StyledTableContainer, StyledTableHeader } from "../../../../../components/styled";
import { useParams, useNavigate } from 'react-router-dom';
import CreateNewButton from '../../../../../components/common/buttons/createNewButton';

const TeampageGroups = () => {
  const { id } = useParams();
  const { CurrentUser } = useContext(AppContext);
  const navigate = useNavigate();

  const [showCreate, setShowCreate] = useState(false);
  const [groups, setGroups] = useState([]);

  useEffect(() => {
    LoadGroup();
    // eslint-disable-next-line
  }, [])

  const LoadGroup = () => {
    if (CurrentUser && CurrentUser.token) {
      axios.get(`${ServerUrl}/api/TeamContentGroup/${id}`, { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }})
      .then(res => {
        setGroups(res.data);
      })
    }
  }

  return (
    <StyledPageContainer>
      <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '20px 20px 10px 20px'}}> 
        <div style={{display: 'flex', gap:"40px", alignItems: 'center'}}>
          <StyledPageTitle>Team Web Content Groups </StyledPageTitle>
          <StyledBackButton onClick={() => navigate(`/teams/${id}/webcontents`)}>Back to Team Web Contents</StyledBackButton>
        </div>
        <CreateNewButton title="New" onClick={() => setShowCreate(prev => !prev)} showCreate={showCreate} />
      </div>
      <Collapse in={showCreate}>
        <CreateNewGroup teamId={id} onCreated={() => { LoadGroup(); setShowCreate(false) }} />
      </Collapse>
      <div style={{padding: '20px'}}>
        {groups.length === 0 && 
          <div>
            No group found. Please add new group.
          </div>
        }
        {groups.length > 0 &&
          <>
          <StyledTableContainer style={{height: '46px'}}>
            <StyledTableHeader>
              <span style={{paddingLeft: '10px'}}>Name</span>
            </StyledTableHeader>

            <div style={{display: 'flex', width: '100px'}}>
            </div>
          </StyledTableContainer>
          {groups.map((group, index) => 
            <GroupRow key={index} teamId={id} item={group} onTaskDone={() => LoadGroup()} />
          )}
          </>
        }
      </div>
    </StyledPageContainer>
  );
}

export default TeampageGroups;
