import React from 'react';
import FlexMobileContainer from '../../components/common/flex/flexMobileContainer';
import { withTheme } from 'styled-components';

const Section = (props) => {
  const { backgroundColor, color } = props.style;
  return (
    <div {...props} style={{width: '100%', backgroundColor, color}}>
      <FlexMobileContainer>
        {props.children}
      </FlexMobileContainer>
    </div>
  )
}

const Unavailable = (props) => {
  return (
    <div>
      <Section style={{backgroundColor: props.theme.home.background1, color: props.theme.home.color}}> 
        <div style={{display: 'flex', padding: '40px 0px', justifyContent: 'center', minHeight: '600px'}}>
            <h2>I am working on this page...</h2>
        </div>
      </Section>
    </div>
  )
}

export default withTheme(Unavailable);