import React, { useEffect, useState } from "react";
import axios from 'axios';
import { useParams } from 'react-router-dom'
import "react-reflex/styles.css";
import styled from 'styled-components';
import { ServerUrl } from '../../../config';
import PageEditor from '../pageEditor/index';
import { StyledTableRow, StyledTableCellBold, StyledTableCell } from '../../../components/styled';
import WithPosition from '../../../components/common/hoc/resetPagePosition';

export const StyledIcon = styled.div`
  display: inline-block;
  padding-top: 4px;
  margin-right: 4px;
  transition-duration: 0.5s;
  transform-origin: center center;
  transform: ${props => props.rotate ? "rotate(0deg)" : "rotate(180deg)"};
  cursor: pointer;

  &:hover {
    background-color: white;
  }
`;

const PublicContentViewer = () => {
  const { id } = useParams();

  const [pageData, setPageData] = useState(null);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  
  useEffect(() => {
    axios.get(`${ServerUrl}/api/WebContent/public/${id}`)
    .then(res => {
      setPageData(res.data);
    })
  }, [id]);


  return (
    <div style={{width: '100%', margin: 'auto', borderLeft: '1px solid #d2d2d2', borderRight: '1px solid #d2d2d2'}}>
      {pageData && 
        <>
          <StyledTableRow style={{backgroundColor: '#e2e5e8'}}>
              <StyledTableCellBold>Name: {pageData.name}</StyledTableCellBold>
              <StyledTableCell>
                Description: {pageData.description}
              </StyledTableCell>
              <div style={{fontSize: '14px', color: 'black'}}>
                <span>LastUpdate: {new Date(pageData.lastUpdate).toLocaleDateString()}</span>
              </div>
          </StyledTableRow>
          <PageEditor content={JSON.parse(pageData.content)} onDataChanged={() => {}}/>
        </>
      }

    </div>
  );
}

export default WithPosition(PublicContentViewer);
