import React, { useState } from 'react';
import styled from 'styled-components';
import EditIcon from '@mui/icons-material/Edit';
import { useNavigate } from 'react-router-dom';
import { StyledTableRow, StyledTableCellBold, StyledTableCell } from '../../../../components/styled';
import Tooltip from '@mui/material/Tooltip';

const StyledDiv = styled.div`
  padding: 6px;
  cursor: pointer;
  &:hover {
    background-color: white;
  }
`;

const GroupRow = ({item, onTaskDone}) => {
  const navigate = useNavigate();
  
  const [isOver, setIsOver] = useState(false);

  return (
    <StyledTableRow onMouseEnter={() => setIsOver(true)} onMouseLeave={() => setIsOver(false)}>
        <StyledTableCellBold>
          <span style={{cursor: 'pointer'}} onClick={()=> navigate(`/public-contents/${item.id}`)}>{item.name}</span>
        </StyledTableCellBold>
        <StyledTableCell>
          {item.description}
        </StyledTableCell>
        <StyledTableCell>
          <span>{new Date(item.lastUpdate).toLocaleDateString()}</span>
        </StyledTableCell>
        <div style={{display: 'flex', width: '50px'}}>
          {isOver &&
            <Tooltip title="Edit content">
              <StyledDiv style={{marginRight: '10px'}}>
                <EditIcon style={{width: '24px', height: '20px', color: '#1976d2'}} onClick={()=> navigate(`/public-contents/${item.id}`)} />
              </StyledDiv>
            </Tooltip>
          }
        </div>
    </StyledTableRow>
  )
}

export default GroupRow;