import React, { useState, useContext, useEffect } from "react";
import Button from '../../components/common/buttons/IconRatationButton';
import Card from "./card";
import axios from 'axios';
import { AppContext } from '../../context/appContext';
import { useNavigate } from 'react-router-dom';
import { ServerUrl, SignInRedirect } from '../../config';
import { TextField, InputAdornment, IconButton } from "@mui/material";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";

const SignIn = () => {
  const [username, setUsername] = useState('');
  const [emailError, setEmailError] = useState(false);
  const [password, setPassword] = useState('');
  const [passwordError, setPasswordError] = useState(false);
  const [showProgress, setShowProgress] = useState(false);
  const [showPassword, setShowPassword] = useState(false);

  const { dispatch } = useContext(AppContext);

  const navigate = useNavigate();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);


  const ValidateEmail = (email) => {
    return /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(email);
  }

  const onSignIn = () => {
    var emailError = false;
    if (!ValidateEmail(username)) {
      emailError = true;
      setEmailError(true);
    }
    var passError = false;
    if (password.length === 0) {
      passError = true;
      setPasswordError(true);
    }

    if (emailError || passError) return;

    setShowProgress(true);
    axios.post(`${ServerUrl}/api/Auth/authenticate`, {username, password}, {
      'Content-Type': 'application/json;charset=UTF-8',
    }).then(res => {
      dispatch({type: 'LOGIN', data: res.data});
      navigate(SignInRedirect)
    }).catch(() => {
      dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: ['Fail to sign in']}})
    }).finally(() => {
      setShowProgress(false);
    });
  }

    return (
      <div style={{minHeight: '600px', paddingBottom: '20px'}}>
      <Card title="Sign In">
        <div style={{fontSize: '18px'}}>Email</div>
        <TextField style={{ width: '100%', marginBottom: '0px'}}
            size="small"
            margin="dense"
            type="text"
            fullWidth
            variant="outlined"
            value={username}
            onChange={(e) => {setUsername(e.target.value); setEmailError(false) }}
        />
        {emailError && <div style={{fontSize: '14px', color: 'red'}}>Invalid email</div>}
        <div style={{fontSize: '18px', marginTop: '16px'}}>Password</div>
        <TextField  style={{ width: '100%'}}
            size="small"
            margin="dense"
            type={showPassword ? "text" : "password"}
            fullWidth
            variant="outlined"
            value={password}
            onChange={(e) => { setPassword(e.target.value); setPasswordError(false)} }
            onKeyUp={(e) => { if (e.key === 'Enter') { onSignIn()} }}
            InputProps={{ 
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={() => setShowPassword(!showPassword)}
                  >
                    {showPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              )
            }}
        />
        {passwordError && <div style={{fontSize: '14px', color: 'red'}}>Password required</div>}
        <div style={{display: 'flex', justifyContent: 'flex-end', margin: '10px 0px 10px 0px'}}>
          <div style={{fontSize: '18px', cursor: 'pointer', borderBottom: '1px solid blue', borderBottomStyle: 'dashed'}}
              onClick={() => navigate('/forget-password')}>
            Forget password?
          </div>
        </div>
        <div style={{display:'flex', justifyContent:'center', margin:'20px 10px 20px 10px'}}>
          <Button showIcon={showProgress} label="Sign In" onClick={onSignIn} 
            style={{color: 'white', backgroundColor:'#1976D3', width: '200px', padding: '12px 20px', margin: 'auto'}} />
        </div>
        <hr />
        <div style={{fontSize: '18px'}}>It is free</div>
        <div style={{display:'flex', justifyContent:'center', margin:'10px 10px 20px 10px'}}>
          <Button label="Create Account" onClick={() => navigate("/signup")}
            style={{color: 'white', backgroundColor:'#04AA6D', width: '200px', padding: '12px 20px', margin: 'auto'}} />
        </div>

      </Card>

      </div>
    );
}

export default SignIn;

