import React, { useEffect, useContext, useState } from 'react';
import { useParams, useNavigate, useSearchParams } from 'react-router-dom';
import axios from 'axios';
import { AppContext } from "../../../../context/appContext";
import { ServerUrl } from "../../../../config";
import { Collapse } from '@mui/material';
import Options from "../../../../components/options";
import TextField from '@mui/material/TextField';
import styled from 'styled-components';
import { StyledBackButton, StyledPageContainer, StyledPageTitle, StyledTableContainer, StyledTableHeader, StyledFindLabel, StyleRotation } from "../../../../components/styled";
import CreateNewPage from './createnewpage';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import PageRow from './pagerow';
import CreateNewButton from '../../../../components/common/buttons/createNewButton';
import SearchIcon from '@mui/icons-material/Search';
import WithPosition from '../../../../components/common/hoc/resetPagePosition';

const StyleLabel = styled.div`
  display: flex;
  align-items: center;
  padding: 5px 10px;
  color: ${props => props.disabled ? "gray" : "#0969DE"};
  cursor: ${props => props.disabled ? "not-allowed" : "pointer"};
  border-radius: 6px;
  border: 1px solid white;

  &:hover {
    border: ${props => props.disabled ? "1px solid white" : "1px solid lightgray"}
  }
`

const TeamContents = () => {
  const navigate = useNavigate();
  
  const { id } = useParams();
  let [searchParams] = useSearchParams();
  let searchPara = searchParams.get("search");
  let groupPara = searchParams.get("group");
  let sortPara = searchParams.get("sort");
  let pagePara = searchParams.get("page");
  let ascPara = searchParams.get("asc");

  const { CurrentUser } = useContext(AppContext);
  const [pages, setPages] = useState([]);
  const [showCreate, setShowCreate] = useState(false);
  const [teamName, setTeamName] = useState('');
  const [groups, setGroups] = useState([]);
  const [selectedGroup, setSelectedGroup] = useState(0);
  const [selectedSort, setSelectedSort] = useState('name');
  const [ascending, setAscending] = useState(true);
  const [searchText, setSearchText] = useState('');
  const [totalWebpages, setTotalWebpages] = useState(0);
  const [totalPages, setTotalPages] = useState(0);
  const [previousPage, setPreviousPage] = useState(false);
  const [nextPage, setNextPage] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  useEffect(() => {
    LoadPages();
    LoadGroup();
    // eslint-disable-next-line
  }, [searchPara, groupPara, sortPara, pagePara, ascPara]);

  useEffect(() => {
    Reload();
    // eslint-disable-next-line
  }, [selectedGroup, selectedSort, currentPage, ascending]);

  const LoadPages = () => {
    axios.get(`${ServerUrl}/api/teamcontent?teamid=${id}&search=${searchPara || ''}&group=${groupPara || 0}&sort=${sortPara || 'name'}&page=${pagePara || 1}&asc=${ascPara || 'true'} `, 
      { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }})
    .then(res => {
      setTeamName(res.data.team);
      setPages(res.data.webpages);
      setTotalWebpages(res.data.total);
      setTotalPages(res.data.totalpages);
      setPreviousPage(res.data.previous);
      setNextPage(res.data.next);
    });    
  }

  const LoadGroup = () => {
    if (CurrentUser && CurrentUser.token) {
      axios.get(`${ServerUrl}/api/TeamContentGroup/${id}`, { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }})
      .then(res => {
        setGroups([{id: 0, name: 'All'}].concat(res.data));
      })
    }
  }

  const Reload = () => {
    navigate(`/teams/${id}/webcontents?search=${searchText}&group=${selectedGroup}&sort=${selectedSort}&page=${currentPage}&asc=${ascending}`); 
  }

  return (
    <StyledPageContainer>
      <div style={{display: 'flex', gap:"40px", alignItems: 'center', padding: '20px 20px 0px 20px'}}>
          <StyledPageTitle>Team: {teamName} </StyledPageTitle>
          <StyledBackButton onClick={() => navigate(`/teams`)}>Back to Teams</StyledBackButton>
      </div>
      <div style={{display: 'flex', justifyContent: 'space-between', padding: '10px 20px'}}>
        <div style={{display: 'flex', flexGrow: '1', marginRight: '20px', alignItems:'center'}}>
          <StyledFindLabel>
            Find
          </StyledFindLabel>
          <div style={{position: 'relative', width: '100%'}}>
            <TextField style={{ flexGrow: '1' }}
                  size="small"
                  margin="dense"
                  type="text"
                  placeholder="Find web contents by name"
                  fullWidth
                  variant="outlined"
                  value={searchText}
                  onChange={(e) => { setCurrentPage(1); setSearchText(e.target.value)}}
                  onKeyUp={(e) => { if (e.key === 'Enter') { Reload()} }}
            >
            </TextField>
            <div style={{position: 'absolute', right: '10px', top: '15px', cursor: 'pointer'}} onClick={() => Reload()}>
              <SearchIcon style={{width: '30px', height: '30px'}} />
            </div>
          </div>
        </div>

        <div style={{display: 'flex', alignItems: 'center'}}>
          <Options name="Group" description="Select group" options={groups} selected={selectedGroup} onOptionSelected={(id) => { setCurrentPage(1); setSelectedGroup(id) }} />
          <CreateNewButton title="New" onClick={() => setShowCreate(prev => !prev)} showCreate={showCreate} />
        </div>
      </div>
      <Collapse in={showCreate}>
        <CreateNewPage teamId={id} onCreated={() => { LoadPages(); setShowCreate(false) }}/>
      </Collapse>
      {pages.length === 0 &&
        <div style={{display: 'flex', justifyContent: 'center'}}>
          No web contents found. Please add new web content.
        </div>
      }

      {pages.length > 0 &&
        <>
          <div style={{padding: '0px 20px'}}>
            <StyledTableContainer>
              <StyledTableHeader onClick={() => { if (selectedSort === 'name') { setAscending(prev => !prev) } else { setSelectedSort('name'); setAscending(true);} }}>
                <span>Name</span>
                  {selectedSort === 'name' && <StyleRotation rotate={ascending.toString()}>
                    <ExpandLessIcon style={{color:'black', width: '30px', height: '30px'}} />
                  </StyleRotation>}
              </StyledTableHeader>
              <StyledTableHeader onClick={() => { if (selectedSort === 'group') { setAscending(prev => !prev) } else { setSelectedSort('group'); setAscending(true);} }}>
                  Group
                  {selectedSort === 'group' && <StyleRotation rotate={ascending.toString()}>
                    <ExpandLessIcon style={{color:'black', width: '30px', height: '30px'}} />
                  </StyleRotation>}
              </StyledTableHeader>
              <StyledTableHeader onClick={() => { if (selectedSort === 'lastupdate') { setAscending(prev => !prev) } else { setSelectedSort('lastupdate'); setAscending(true);} }}>
                  Last Update
                  {selectedSort === 'lastupdate' && <StyleRotation rotate={ascending.toString()}>
                    <ExpandLessIcon style={{color:'black', width: '30px', height: '30px'}} />
                  </StyleRotation>}
              </StyledTableHeader>
              <StyledTableHeader onClick={() => { if (selectedSort === 'description') { setAscending(prev => !prev) } else { setSelectedSort('description'); setAscending(true);} }}>
                  Description
                  {selectedSort === 'description' && <StyleRotation rotate={ascending.toString()}>
                    <ExpandLessIcon style={{color:'black', width: '30px', height: '30px'}} />
                  </StyleRotation>}
              </StyledTableHeader>

              <div style={{display: 'flex', width: '100px'}}>
              </div>
            </StyledTableContainer>
            {pages.map((page, index) => 
              <PageRow key={index} teamId={id} item={page} onTaskDone={LoadPages} />
            )}
          </div>
          <div style={{display: 'flex', justifyContent: 'space-between', padding: '20px'}}>
            <div>
              {totalWebpages} web contents
            </div>
            <div style={{display: 'flex', justifyContent: 'center'}}>
              <StyleLabel disabled={!previousPage} style={{marginRight: '20px'}} onClick={() => {
                if (previousPage) {
                  setCurrentPage(currentPage - 1);
                }
              }}>
                <ChevronLeftIcon style={{width: '26px', height: '26px'}} />
                <span style={{marginBottom: '3px'}}>Previous</span>
              </StyleLabel>
              <StyleLabel disabled={!nextPage} onClick={() => {
                if (nextPage) {
                  setCurrentPage(currentPage + 1)
                }
              }}>
                <span style={{marginBottom: '3px'}}>Next</span><ChevronRightIcon style={{width: '26px', height: '26px'}} />
              </StyleLabel>
            </div>
            <div>
              Page {currentPage} of {totalPages}
            </div>
          </div>
        </>
      }
    </StyledPageContainer>    
  );

}

export default WithPosition(TeamContents);