import React, { useState, useContext } from 'react';
import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import { AppContext } from '../../../../../context/appContext';

const RemoveSpaceRight = (text) => {
    text = text.replace('\n', '').replace('\r', '');

    var pos = text.lastIndexOf('>', text.length - 1);
    while (pos > 0) {
        var endPos = pos + 1;
        var count = 0;
        while ((text.charCodeAt(endPos) === 10 || text.charCodeAt(endPos) === 32) && endPos < text.length) {
            endPos++; 
            count++;
        }
        if (count > 0) {
            text = text.substring(0, pos + 1) + text.substring(pos + count + 1, text.length);
        }
    
        pos = text.lastIndexOf('>', pos - 1);
    }
    return text;
}

const RemoveSpaceLeft = (text) => {
    text = text.replace('\n', '').replace('\r', '');

    var pos = text.lastIndexOf('<', text.length - 1);
    while (pos > 0) {
        var endPos = pos - 1;
        var count = 0;
        while ((text.charCodeAt(endPos) === 10 || text.charCodeAt(endPos) === 32) && endPos >= 0) {
            endPos--; 
            count++;
        }
        if (count > 0) {
            text = text.substring(0, pos - count) + text.substring(pos, text.length);
        }
    
        pos = text.lastIndexOf('<', pos - 1);
    }
    return text;
}

const GetNodeInfo = (node) => {
    let obj = {};
    obj['isChecked'] = true;
    obj['element'] = node.nodeName;
    obj['children'] = [];
    obj['content'] = '';
    obj['name'] = '';
    obj['attributes'] = {};
    obj['style'] = '';
    for (var i = 0; i < node.childNodes.length; i++) {
        if (node.childNodes[i].nodeName === '#text') 
        {
            obj['content'] = node.childNodes[i].nodeValue;    
        } 
        else 
        {
            let child = GetNodeInfo(node.childNodes[i]);
            obj['children'].push(child);        
        }
    }

    let attributes = node.attributes;
    var attributesObj = {};
    if (attributes && attributes.length > 0) {
      for(let j = 0; j < attributes.length; j++) {
          if (attributes[j].nodeName === 'style') {
              obj['style'] = formatStyle(attributes[j].nodeValue);
          } else {
              attributesObj[attributes[j].nodeName] = attributes[j].nodeValue;
          }
      }
    }
    obj["attributes"] = attributesObj
    return obj;
}


const formatStyle = (text) => {
    var result = {};
    var items = text.split(";");
    if (items.length > 0) {
        for (var i = 0; i < items.length; i++) {
            var keyValue = items[i].split(":");
            if (keyValue.length === 2 && keyValue[0].trim().length > 0 && keyValue[1].trim().length > 0) {
                result[keyValue[0].trim()] = keyValue[1].trim();
            }
        }
    }
    return result;
}

const ImportDialog = ({node, onClose, onImportNode}) => {
    const [htmlText, setHtmlText] = useState('');
    const { dispatch } = useContext(AppContext);

    if (!node) {
      return null;
    }

    const onImport = () => {
      var parser = new DOMParser();
      var text = RemoveSpaceRight(RemoveSpaceLeft(htmlText));
      var xmlDoc = parser.parseFromString(text, "text/xml");
      const errorNode = xmlDoc.querySelector('parsererror');
      if (errorNode) {
        dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: ["Fail to import HTML"]}})
      }
      else {
        var obj = GetNodeInfo(xmlDoc.documentElement);
        onImportNode(obj);  
      }      
    }

    return (
        <Dialog maxWidth={'lg'} fullWidth={true} open={node !== null}>
            <DialogTitle>
                Import HTML
            </DialogTitle>
            <DialogContent dividers>
                <div style={{display: 'flex', gap: '10px'}}>
                    <TextField  style={{ flex: '1 1', marginBottom: '16px'}}
                        multiline
                        rows={16}
                        margin="dense"
                        id="html"
                        label="HTML"
                        type="text"
                        fullWidth
                        variant="outlined"
                        value={htmlText}
                        size="small"
                        InputLabelProps={{
                            shrink: true,
                          }}
                        onChange={(e) => setHtmlText(e.target.value)}
                    />
                </div>

            </DialogContent>
            <DialogActions>
                <Button onClick={() => onClose()}>Cancel</Button>
                {htmlText.length > 0 && <Button onClick={onImport}>Import</Button>}
                {htmlText.length === 0 && <Button disabled>Import</Button>}
            </DialogActions>
        </Dialog>
    )
}
  
export default ImportDialog;
