import React, { useContext } from "react";
import { BrowserRouter, Route, Routes, Navigate } from "react-router-dom";
import HomeComponent from './pages/home/index';
import MyHome from './pages/yourHome/index';
import Documentation from './pages/docs/index';
import DocTopic from './pages/docs/docTopic';
import TeamsComponent from './pages/repos/teams/index';
import TeamHome from './pages/repos/teams/teamHome';
import TeamManageComponent from './pages/repos/teams/teamUsers/teamManage';
import TeamWebContents from "./pages/repos/teams/webContents/index";
import TeamWebContent from "./pages/repos/teams/webContents/webContent";
import TeamWebContentGroups from "./pages/repos/teams/webContents/group/index";
import Webcontents from './pages/repos/webContents/index';
import WebContentGroups from './pages/repos/webContents/group/index';
import WebContent from './pages/repos/webContents/webContent';
import SignIn from "./pages/auth/signin";
import SignUp from "./pages/auth/signup";
import ChangePassword from './pages/auth/changepassword';
import ForgetPassword from './pages/auth/forgetpassword';
import ResetPassword from './pages/auth/resetpassword';
import ActivateAccount from './pages/auth/activateaccount';
import ContactUs from "./pages/auth/contactus";
import UnavailableComponent from "./pages/unavailable";
import PublicContent from "./pages/repos/publicContents";
import PublicContentViewer from "./pages/repos/publicContents/viewer";
import Header from "./header";
import Footer from './footer';
import { AppContext } from './context/appContext';
import { ThemeProvider } from "styled-components";
import { themes } from "./theme/themes"
import { GlobalStyles } from './theme/globalStyles';

const AppComponent = () => {
  const { CurrentUser, CurrentTheme } = useContext(AppContext);

  return (
    <ThemeProvider theme={themes[CurrentTheme]}>
      <GlobalStyles />
      <BrowserRouter>
        <Header />
        <div style={{minHeight: '400px'}}>
        <Routes>
          <Route path="/" element={<HomeComponent />} />
          <Route path="/signin" element={<SignIn />} />
          <Route path="/signup" element={<SignUp />} />
          <Route path="/forget-password" element={<ForgetPassword />} />
          <Route path="/reset-password" element={<ResetPassword />} />
          <Route path="/activate-account" element={<ActivateAccount />} />
          <Route path="/contact-us" element={<ContactUs />} />
          <Route path="/working" element={<UnavailableComponent />} />
          <Route path="/docs" element={<Documentation />}>
            <Route path=":topic" element={<DocTopic />} />
          </Route>
          <Route path="/public-contents" element={<PublicContent />} />
          <Route path="/public-contents/:id" element={<PublicContentViewer />} />
          <Route exact path="/yourHome" element={
            <ProtectedRoute user={CurrentUser}>
              <MyHome />
            </ProtectedRoute>
          } />
          <Route exact path="/changepassword" element={
            <ProtectedRoute user={CurrentUser}>
              <ChangePassword />
            </ProtectedRoute>
          } />
          <Route exact path="/teams" element={
            <ProtectedRoute user={CurrentUser}>
              <TeamsComponent />
            </ProtectedRoute>
          } />
          <Route exact path="teams/:id/home" element={
            <ProtectedRoute user={CurrentUser}>
              <TeamHome />
            </ProtectedRoute>
          } />          
          <Route exact path="teams/:id/webcontents/group" element={
            <ProtectedRoute user={CurrentUser}>
              <TeamWebContentGroups />
            </ProtectedRoute>
          } />
          <Route exact path="teams/:id/webcontents" element={
            <ProtectedRoute user={CurrentUser}>
              <TeamWebContents />
            </ProtectedRoute>
          } />
          <Route exact path="teams/:id/manage" element={
            <ProtectedRoute user={CurrentUser}>
              <TeamManageComponent />
            </ProtectedRoute>
          } />
          <Route exact path="teams/:teamId/webcontents/:pageId" element={
            <ProtectedRoute user={CurrentUser}>
              <TeamWebContent />
            </ProtectedRoute>
          } />
          <Route exact path="/webcontents" element={
            <ProtectedRoute user={CurrentUser}>
              <Webcontents />
            </ProtectedRoute>
          } />
          <Route exact path="/webcontents/group" element={
            <ProtectedRoute user={CurrentUser}>
              <WebContentGroups />
            </ProtectedRoute>
          } />
          <Route exact path="/webcontents/:id" element={
            <ProtectedRoute user={CurrentUser}>
              <WebContent />
            </ProtectedRoute>
          } />
          <Route path="*" element={<Navigate replace to="/" />} />
        </Routes>
        </div>
        <Footer />

      </BrowserRouter>
    </ThemeProvider>
  );
}


const ProtectedRoute = ({ user, children }) => {
  if (!user) {
    return <Navigate to="/" replace />;
  }

  return children;
};

export default AppComponent;

