import React, { useEffect, useState, useContext } from "react";
import axios from 'axios';
import { useParams } from 'react-router-dom'
import TextField from '@mui/material/TextField';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import SaveIcon from '@mui/icons-material/Save';
import "react-reflex/styles.css";
import styled from 'styled-components';
import { ServerUrl } from '../../../config';
import { AppContext } from '../../../context/appContext';
import { StyledButton, StyledFlexContainer } from '../../../components/styled';
import PageEditor from '../pageEditor/index';
import Checkbox from '@mui/material/Checkbox';
import WithPosition from '../../../components/common/hoc/resetPagePosition';
import { getHTML } from '../pageEditor/helper/htmlHelper';

export const StyledIcon = styled.div`
  display: inline-block;
  padding-top: 4px;
  margin-right: 4px;
  transition-duration: 0.5s;
  transform-origin: center center;
  transform: ${props => props.rotate ? "rotate(0deg)" : "rotate(180deg)"};
  cursor: pointer;

  &:hover {
    background-color: white;
  }
`;

const WebContent = () => {
  const { CurrentUser, dispatch } = useContext(AppContext);
  const { id } = useParams();

  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const [groupId, setGroupId] = useState(null);
  const [groups, setGroups] = useState([]);
  const [isPublic, setIsPublic] = useState(false);
  const [dataId, setDataId] = useState(0);
  const [treeData, setTreeData] = useState(null);

  const onDataChanged = (data) => {
    setTreeData(data);
  };

  const LoadGroup = () => {
    if (CurrentUser && CurrentUser.token) {
      axios.get(`${ServerUrl}/api/WebContentGroup`, { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }})
      .then(res => {
        setGroups(res.data);
      })
    }
  }

  const LoadWebpage = () => {
    if (CurrentUser && CurrentUser.token && id > 0) {
      axios.get(`${ServerUrl}/api/WebContent/${id}`, { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }})
      .then(res => {
        setName(res.data.name);
        setDataId(res.data.dataId);
        setIsPublic(res.data.isPublic);
        setDescription(res.data.description);
        setGroupId(res.data.groupId);
        setTreeData(JSON.parse(res.data.content));
      })
    }
  }

  useEffect(() => {
    LoadWebpage();
    LoadGroup();
    window.scrollTo(0, 0);
    // eslint-disable-next-line
  }, []);

  const SavePage = () => {
    axios.put(`${ServerUrl}/api/WebContent/${id}`, {name, description, isPublic, groupId, content: JSON.stringify(treeData), html: getHTML(treeData)}, 
      { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }, 'Content-Type': 'application/json;charset=UTF-8'})
    .then(res => {
      dispatch({type:'SET_MESSAGE', data: {title: 'Success', lines: ['Web content is saved successfully']}})
    }).catch(() => {
      dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: ['Fail to save webpage']}})
    });
  }  

  const SwitchPublic =() => {
    var newPublic = !isPublic;
    setIsPublic(newPublic);
    if (newPublic) {
      dispatch({type:'SET_MESSAGE', data: {title: 'Confirmation', lines: ['Web content will be public after saved.', `Here is the link: https://www.w3element.com/public-contents/${dataId}`]}})
    } 
  }

  return (
    <div style={{width: '100%', height: 'calc(100vh - 60px)'}}>
      <div style={{borderBottom: '2px solid #e2e5e8', padding: '0px 10px'}}>
        <StyledFlexContainer width="1024px" style={{gap: '10px'}}>
          <StyledFlexContainer style={{flex: '2', gap: '10px'}}>
            <StyledFlexContainer style={{gap: '10px'}}>
              <div>Name</div>
              <TextField
                    size="small"
                    margin="dense"
                    type="text"
                    fullWidth
                    variant="outlined"
                    value={name}
                    onChange={(e) => { setName(e.target.value); }}
              />
            </StyledFlexContainer>
            <StyledFlexContainer style={{gap: '10px'}}>
              <div>Group</div>
              <Select size="small"
                  labelId="demo-select-small"
                  id="demo-select-small"
                  value={groupId}
                  onChange={(e) => { setGroupId(e.target.value); }}
                  style={{width: '100%'}}
                  displayEmpty
                  inputProps={{ 'aria-label': 'Without label' }}
                  >
                  {groups.map((item) => {
                    return <MenuItem key={item.id} value={item.id}>{item.name}</MenuItem>
                  })}
              </Select>
            </StyledFlexContainer>
          </StyledFlexContainer>
          <StyledFlexContainer style={{flex: '3', gap: '10px'}}>
            <StyledFlexContainer style={{flex: '1', gap: '10px'}}>
              <div>Description</div>
              <TextField  style={{ width: '100%'}}
                size="small"
                margin="dense"
                type="text"
                fullWidth
                variant="outlined"
                value={description}
                onChange={(e) => { setDescription(e.target.value); }}
              />
            </StyledFlexContainer>
            <div>
              <span style={{marginRight: '0px'}}>Public</span>
              <Checkbox checked={isPublic} onChange={SwitchPublic} inputProps={{ 'aria-label': 'controlled' }} />
            </div>
            <StyledButton backColor={'#04AA6D'} 
              backOverColor={'#059862'} 
              style={{alignItems: 'center', cursor: "pointer"}} onClick={SavePage}>
                <SaveIcon style={{width: '20px', height: '20px', color: 'white', marginRight: '10px'}} />
                <span>Save</span>
            </StyledButton>
          </StyledFlexContainer>
        </StyledFlexContainer>
      </div>

      {treeData && 
        <PageEditor content={treeData} onDataChanged={onDataChanged}/>
      }

    </div>
  );
}

export default WithPosition(WebContent);
