import React, { useState, useContext } from 'react';
import TextField from '@mui/material/TextField';
import {v4 as uuidv4} from 'uuid';
import RefreshIcon from '@mui/icons-material/Refresh';
import Tooltip from '@mui/material/Tooltip';
import { StyledAddButton, StyledButton } from '../../../../components/styled';
import { AppContext } from '../../../../context/appContext';
import { ServerUrl } from '../../../../config';
import axios from 'axios';

const PublishPage = ({data}) => {
  const [published, setPublished] = useState(data.published);
  const [publishKey, setPublishKey] = useState(data.publishKey || '');
  const { dispatch, CurrentUser } = useContext(AppContext);

  const generateGuid = () => {
    setPublishKey(uuidv4().toString())
  }

  const onPublish = () => {
    if (publishKey.length === 0) {
      dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: ["Publish key is required."]}});
      return;
    }

    axios.post(`${ServerUrl}/api/WebContent/publish`, {id: data.id, key: publishKey}, 
    { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }, 'Content-Type': 'application/json;charset=UTF-8'})
    .then(res => {
      dispatch({type:'SET_MESSAGE', data: {title: 'Success', lines: ["Web content is published."]}});
      setPublished(true);
    }).catch((err) => {
        dispatch({ type: 'SET_MESSAGE', data: { title: 'Error', lines: ["Fail to publish web content.", err.response.data]}})
    })
  }

  const onUnpublish = () => {
    if (publishKey.length === 0) {
      dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: ["Publish key is required."]}});
      return;
    }

    axios.post(`${ServerUrl}/api/WebContent/unpublish`, {id: data.id, key: publishKey}, 
    { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }, 'Content-Type': 'application/json;charset=UTF-8'})
    .then(res => {
      dispatch({type:'SET_MESSAGE', data: {title: 'Success', lines: ["Web content is unpublished."]}})
      setPublished(false);
    }).catch(() => {
      dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: ["Fail to unpublish web content."]}})
    }) 
  }

  const getRootUrl = () => {
    let href = window.location.href;
    let pathname = window.location.pathname;
    let index = href.indexOf(pathname);
    if (index > 0) {
        return href.substring(0, index) + `/api/content?key=${publishKey}&type=html`;
    }
    return "";
  }

  return (
    <div style={{
      padding: '10px', 
      fontSize: '14px',
      }} >
      <div style={{display: 'flex', alignItems: 'center', gap: '30px'}}>
        <div style={{display: 'flex', alignItems: 'center', gap: '20px'}}>
          Published: 
          {published && 
            <StyledButton style={{ paddingLeft: '10px', paddingRight: '10px', backgroundColor: '#04AA6D'}}>
              <span style={{fontSize: '12px'}}>Yes</span>
            </StyledButton>}
          {!published && 
            <StyledButton style={{ paddingLeft: '10px', paddingRight: '10px', backgroundColor: '#1976d2'}}>
              <span style={{fontSize: '12px'}}>No</span>
            </StyledButton>}
        </div>
        <span>Last Publish: {new Date(data.lastPublish).toLocaleDateString()}</span>
        <span>Last Update: {new Date(data.lastUpdate).toLocaleDateString()}</span>
      </div>

      <div style={{display: 'flex', alignItems: 'center', gap: '10px'}}>
        <div style={{width: '25px'}}>Key</div>
        <div style={{position: 'relative'}}>
          <TextField style={{ width: '400px', backgroundColor: 'white' }}
            size="small"
            margin="dense"
            type="text"
            fullWidth
            variant="outlined"
            value={publishKey}
            onChange={(e) => setPublishKey(e.target.value)}
          />

          <div style={{position: 'absolute', right: '10px', top: '13px', cursor: 'pointer'}} onClick={generateGuid}>
            <Tooltip title="Generate new key">
              <RefreshIcon style={{width: '28px', height: '28px'}} />
            </Tooltip>
          </div>
        </div>
        <StyledAddButton onClick={onPublish}>
          <span>Publish</span>
        </StyledAddButton>
        <StyledButton style={{ paddingLeft: '20px', paddingRight: '20px', backgroundColor: '#1976d2'}} onClick={onUnpublish}>
          <span style={{fontSize: '16px'}}>Unpublish</span>
        </StyledButton>
      </div>
      <div style={{display: 'flex', alignItems: 'center', gap: '10px'}}>
        <div style={{width: '25px'}}>Link</div>
        <TextField style={{ flex: '1', marginBottom: '8px', backgroundColor: 'white'}}
                size="small"
                margin="dense"
                type="text"
                fullWidth
                multiline
                variant="outlined"
                value={getRootUrl()}
                inputProps={{ readOnly: true, }}
        />
      </div>
    </div>    
  )
}

export default PublishPage;