import React from 'react';

const convertToReact = (str) => {
  let ss = str.split("-");
  if (ss.length === 2) {
    let index = str.indexOf("-");
    if (index > 0 && index < str.length - 1) {
      return ss[0] + ss[1][0].toUpperCase() + ss[1].substring(1);
    } 
  }
  return str;
}

const getSpaces = (level) => {
  var str = '    ';
  let num = level * 2;
  for (var i = 0; i < num; i++) {
    str += ' ';
  }
  return str;
};

const GetContents = (node, contents, cssContents, level) => {
  let spaces = getSpaces(level);
  if (node) {
    if (node.style && Object.keys(node.style).length > 0) {
      let newStyle = {};

      for (const [key, value] of Object.entries(node.style)) {
        newStyle[convertToReact(key.toLowerCase())] = value.toLowerCase();
      }
      var jsonStyle = JSON.stringify(newStyle).replace(/"([^"]+)":/g, '$1:');
      
      if (node.attributes) {
        var attributesTextA = '';
        for (const [key, value] of Object.entries(node.attributes)) {
          if (key === 'class') {
            attributesTextA += `className=${value} `;
          } else {
            attributesTextA += `${key}=${value} `;
          }
        }
        attributesTextA = attributesTextA.trimEnd();
        contents.push([`${spaces}<${node.element} ${attributesTextA} style={${jsonStyle}}>`]);
      } else {
        contents.push([`${spaces}<${node.element} style={${jsonStyle}}>`]);
      }
    }
    else {
      if (node.attributes) {
        var attributesText = '';
        for (const [key, value] of Object.entries(node.attributes)) {
          if (key === 'class') {
            attributesText += `className=${value} `;
          } else {
            attributesText += `${key}=${value} `;
          }
        }
        attributesText = attributesText.trimEnd();
        contents.push([`${spaces}<${node.element} ${attributesText}>`]);
      } else {
        contents.push([`${spaces}<${node.element}>`]);
      }
    }
    if (node.content) {
      contents.push([`  ${spaces}${encodeText(node.content)}`]);
    }
    if (node.css && node.css.trim().length > 0) {
      cssContents.push(node.css.trim());
    }
  }
  if (node && node.children) {
    node.children.forEach(element => {
      GetContents(element, contents, cssContents, level + 1);
    });
  }
  if (node) {
    contents.push([`${spaces}</${node.element}>`]);
  }
}

const encodeText = (text) => {
  let textArea = document.createElement('textarea');
  textArea.innerText = text;
  let encodedOutput=textArea.innerHTML;
  let arr=encodedOutput.split('<br>');
  encodedOutput=arr.join('\n');
  return encodedOutput;
}

// const decodeHTMLEntities = (text) => {
//   let textArea = document.createElement('textarea');
//   textArea.innerHTML = text;
//   return textArea.value;
// }

const ReactRender = ({data}) => {
  var contents = [];
  var cssContents = [];
  GetContents(data, contents, cssContents, 0);

  var cssText = "";
  if (cssContents.length > 0) {
    cssText = cssContents.join('\r\n');
  }

  if (cssText.length > 0) {
    cssText = "// beginning of css section\r\n// save the text in this section to styles.css\r\n" + cssText + "\r\n// end of css section\r\n";
    cssText += "\r\n";
  }

  return (
    <pre>
      {cssText}
      {"import React from \"react\";\r\n"}
      {cssText.length > 0 && "import \"./styles.css\"\r\n"}
      {"\r\nconst Element = () => {\r\n"}
      {"  return (\r\n"}
      {contents.join('\r\n') + '\r\n'}
      {"  )\r\n"}
      {"};\r\n\r\n"}
      {"export default Element;\r\n"}
    </pre>
  )
}

export default ReactRender;