import React from 'react';
import { getHTML } from '../helper/htmlHelper';

const HtmlRender = ({data, selectedId}) => {

  var htmlText = getHTML(data, selectedId);

  return (
    <>
      {/* <iframe title="test" width='100%' height="950px" srcDoc={htmlText} frameBorder="0" ></iframe> */}
      <div style={{minHeight: '900px'}}
        dangerouslySetInnerHTML={{__html: htmlText}}
      />
    </>
  )
}

export default HtmlRender;