
const getStyleText = (style) => {
  var str = '';
  for (const [key, value] of Object.entries(style)) {
    str += key + ":" + value + ";";
  }
  return str;
}

const getSpaces = (level) => {
  var str = '';
  let num = level * 2;
  for (var i = 0; i < num; i++) {
    str += ' ';
  }
  return str;
};

const GetContents = (node, contents, cssContents, level, selectedId) => {
  let spaces = getSpaces(level);
  let selected = node && node.id === selectedId && selectedId > 0;

  if (node) {
    if (node.style && Object.keys(node.style).length > 0) {
      if (node.attributes) {
        var attributesTextA = '';
        for (const [key, value] of Object.entries(node.attributes)) {
          attributesTextA += `${key}="${value}" `;
        }
        attributesTextA = attributesTextA.trimEnd();            
        if (selected) {
          contents.push([`${spaces}<${node.element} ${attributesTextA} style="${getStyleText(node.style)} outline: 2px solid red; outline-offset: 4px;">`]);
        } else {
          contents.push([`${spaces}<${node.element} ${attributesTextA} style="${getStyleText(node.style)}">`]);
        }
      }
      else {
        if (selected) {
          contents.push([`${spaces}<${node.element} style="${getStyleText(node.style)} outline: 2px solid red; outline-offset: 4px;">`]);
        } else {
          contents.push([`${spaces}<${node.element} style="${getStyleText(node.style)}">`]);
        }
      }
    }
    else 
    {
      if (node.attributes) {
        var attributesText = '';
        for (const [key, value] of Object.entries(node.attributes)) {
          attributesText += `${key}="${value}" `;
        }
        attributesText = attributesText.trimEnd();          
        if (selected) {
          contents.push([`${spaces}<${node.element} ${attributesText}>`]);
        } else {
          contents.push([`${spaces}<${node.element} ${attributesText}>`]);
        }
      } else {
        if (selected) {
          contents.push([`${spaces}<${node.element} style="outline: 2px solid red; outline-offset: 4px;">`]);
        } else {
          contents.push([`${spaces}<${node.element}>`]);
        }
      }
    }
    if (node.content) {
      contents.push([`${encodeText(node.content)}`]);
    }
    if (node.css && node.css.trim().length > 0) {
      cssContents.push(node.css.trim());
    }
  }
  if (node && node.children) {
    node.children.forEach(element => {
      GetContents(element, contents, cssContents, level + 1, selectedId);
    });
  }
  if (node) {
    contents.push([`${spaces}</${node.element}>`]);
  }
}

const encodeText = (text) => {
  let textArea = document.createElement('textarea');
  textArea.innerText = text;
  let encodedOutput=textArea.innerHTML;
  let arr=encodedOutput.split('<br>');
  encodedOutput=arr.join('\n');
  return encodedOutput;
}

