export const getHTML = (treeData, selectedId) => {
  var data = JSON.parse(JSON.stringify(treeData));
  // generateJSON(data);

  var contents = [];
  var cssContents = [];
  GetContents(data, contents, cssContents, 0, selectedId);
  if (cssContents.length > 0) {
    cssContents.unshift("<style>");
    cssContents.push("</style>");
  }

  var result = '<html>\r\n' + 
      '<head>\r\n' + 
      cssContents.join('\r\n') + '\r\n' +
      '</head>\r\n' +
      '<body>\r\n' +
      contents.join('\r\n') + '\r\n' +
      '</body>\r\n' +
      '</html>\r\n';

  return result;
}

