import React from 'react';
import { CircularProgress } from '@mui/material';

const Loading = ({text}) => {
  return (
    <div style={{width: '100%'}}>
      <div style={{width: '160px', padding: '20px', margin:'auto', 
        display:'flex', flexDirection:'column', justifyContent:'center', alignItems:'center'}}>
        <CircularProgress />
        {!text && <span style={{marginTop:'20px'}}>Loading...</span>}
        {text && <span style={{marginTop:'20px'}}>{text}</span>}
      </div>
    </div>
  )
};

export default Loading;
