import React from 'react';
import styled from 'styled-components';

const CardContainer = styled.div`
border-radius: 6px;
box-shadow: 0 0 4px 4px rgba(200, 200, 200, .5);
width: 400px;
margin: auto;
padding: 20px;
`;

const Title = styled.div`
font-size: 24px;
font-weight: bold;
margin-bottom: 30px;
`;

const Card = (props) => {
  const { title } = props;

  return (
    <div style={{margin: '40px 0px 200px 0px'}}>
      <CardContainer {...props}>
        <Title>{title}</Title>
        {props.children}
      </CardContainer>
    </div>
  )
}

export default Card;