import React, { useEffect, useState, useContext } from 'react';
import axios from "axios";
import { ServerUrl } from "../../../config";
import { AppContext } from "../../../context/appContext";
import { useParams, useNavigate } from 'react-router-dom';
import PeopleIcon from '@mui/icons-material/People';
import DeleteIcon from '@mui/icons-material/Delete';
import AccountTreeIcon from '@mui/icons-material/AccountTree';
import Loading from '../../../components/common/loading';
import { StyledBox, StyledBackButton, StyledBoxContainer, StyledPageTitle } from '../../../components/styled/index';

const TeamHome = () => {
  const { id } = useParams();

  const [teamName, setTeamName] = useState('');
  const [loading, setLoading] = useState(false);
  const { CurrentUser } = useContext(AppContext);
  const { token } = CurrentUser;
  const navigate = useNavigate();

  const LoadTeams = () => {
    setLoading(true);
    axios.get(`${ServerUrl}/api/teams/${id}/info`, 
      { headers: { 'Authorization' : 'Bearer ' + token }})
    .then(res => {
      setTeamName(res.data.name);    
    }).catch(() => alert('error')).finally(() => setTimeout(() => setLoading(false), 1000));    
  };

  useEffect(() => {
    window.scrollTo(0, 0);
    LoadTeams();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <div style={{minHeight: '800px', width: '80%', margin: 'auto'}}>
      {loading && <Loading />}
      {!loading && <div style={{padding: '20px'}}>
        <div style={{display: 'flex', gap:"40px", alignItems: 'center'}}>
          <StyledPageTitle>Team - {teamName}</StyledPageTitle>
          <StyledBackButton onClick={() => navigate("/teams")}>Back to My Teams</StyledBackButton>
        </div>
        <StyledBoxContainer>
          <StyledBox onClick={() => navigate(`/teams/${id}/manage`)}>
            <PeopleIcon style={{width: '60px', height: '60px', color: '#008B8B'}} />
            <span style={{fontSize: '16px', fontWeight: 'bold'}}>Manage members</span>
          </StyledBox>
          <StyledBox onClick={() => navigate(`/teams/${id}/webcontents`)}>
            <AccountTreeIcon style={{width: '60px', height: '60px', color: '#008B8B'}} />
            <span style={{fontSize: '16px', fontWeight: 'bold'}}>Web Contents</span>
          </StyledBox>
          <StyledBox onClick={() => navigate('/webcontents')}>
            <DeleteIcon style={{width: '60px', height: '60px', color: '#d32f2f'}} />
            <span style={{fontSize: '16px', fontWeight: 'bold'}}>Remove</span>
          </StyledBox>
        </StyledBoxContainer>
      </div>}
    </div>
  )
}

export default TeamHome;
