import React, { useState, useContext, useEffect } from "react";
import TextField from '@mui/material/TextField';
import Button from '../../components/common/buttons/IconRatationButton';
import Card from "./card";
import axios from 'axios';
import { AppContext } from '../../context/appContext';
import { ServerUrl } from '../../config';

const ContactUs = () => {
  const [email, setEmail] = useState('');
  const [subject, setSubject] = useState('');
  const [message, setMessage] = useState('');
  const [emailError, setEmailError] = useState(false);
  const [subjectError, setSubjectError] = useState(false);
  const [messageError, setMessageError] = useState(false);
  const [showProgress, setShowProgress] = useState(false);

  const { dispatch } = useContext(AppContext);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const ValidateEmail = (email) => {
    return /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(email);
  }

  const onForgetPassword = () => {
    var emailError = false;
    var subjectError = false;
    var messageError = false;
    
    if (!ValidateEmail(email)) {
      emailError = true;
      setEmailError(true);
    }

    if (subject.trim().length === 0) {
      subjectError = true;
      setSubjectError(true);
    }

    if (message.trim().length === 0) {
      messageError = true;
      setMessageError(true);
    }

    if (emailError || subjectError || messageError) return;

    try
    {
      setShowProgress(true);
      axios.post(`${ServerUrl}/api/Auth/contactus`, {email, subject, message}, {
        'Content-Type': 'application/json;charset=UTF-8',
      }).then(res => {
        dispatch({type:'SET_MESSAGE', data: {title: 'Success', lines: ["Your message is send to us successfully. Thank you."]}})
      }).catch((err) => {
        dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: [err.response.data]}})
      }).finally(() => {
        setShowProgress(false);
      });
    }
    catch 
    {
      dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: ["Fail to send your message."]}})
    }
  }

    return (
      <div style={{minHeight: '600px'}}>
      <Card title="Contact us" style={{width: '600px'}}>
        <div style={{fontSize: '18px'}}>Email</div>
        <TextField style={{ width: '100%', marginBottom: '0px'}}
            size="small"
            margin="dense"
            type="text"
            fullWidth
            variant="outlined"
            value={email}
            onChange={(e) => {setEmail(e.target.value); setEmailError(false) }}
        />
        {emailError && <div style={{fontSize: '14px', color: 'red'}}>Invalid email</div>}

        <div style={{fontSize: '18px', marginTop: '10px'}}>Subject</div>
        <TextField style={{ width: '100%', marginBottom: '0px'}}
            size="small"
            margin="dense"
            type="text"
            fullWidth
            variant="outlined"
            value={subject}
            onChange={(e) => {setSubject(e.target.value); setSubjectError(false) }}
        />
        {subjectError && <div style={{fontSize: '14px', color: 'red'}}>Subject is required</div>}

        <div style={{fontSize: '18px', marginTop: '10px'}}>Message</div>
        <TextField style={{ width: '100%', marginBottom: '0px'}}
            multiline
            rows={10}
            size="small"
            margin="dense"
            type="text"
            fullWidth
            variant="outlined"
            value={message}
            onChange={(e) => {setMessage(e.target.value); setMessageError(false) }}
        />
        {messageError && <div style={{fontSize: '14px', color: 'red'}}>Message is required</div>}

        <div style={{display:'flex', justifyContent:'center', margin:'20px 10px 20px 10px'}}>
          <Button showIcon={showProgress} label="Send" onClick={onForgetPassword} 
            style={{color: 'white', backgroundColor:'#1976D3', width: '160px', padding: '12px 20px', margin: 'auto'}} />
        </div>
      </Card>
      </div>
    );
}

export default ContactUs;

