import React, { useState, useContext, useEffect } from "react";
import Button from '../../components/common/buttons/IconRatationButton';
import Card from "./card";
import axios from 'axios';
import { AppContext } from '../../context/appContext';
import { ServerUrl } from '../../config';
import { TextField, InputAdornment, IconButton } from "@mui/material";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";

const SignUp = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [showProgress, setShowProgress] = useState(false);
  const [passwordError, setPasswordError] = useState(false);
  const [confirmPasswordError, setConfirmPasswordError] = useState(false);
  const [emailError, setEmailError] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);

  const { dispatch } = useContext(AppContext);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const ValidateEmail = (email) => {
    return /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(email);
  }

  const ValidatePassword = (s) => {
    return /^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z]).{6,32}$/.test(s);
  }

  const onCreateAccount = () => {
    var emailError = false;
    if (!ValidateEmail(username)) {
      emailError = true;
      setEmailError(true);
    }
    var passError = false;
    if (!ValidatePassword(password)) {
      passError = true;
      setPasswordError(true);
    }

    var confirmPassError = false;
    if (confirmPassword !== password) {
      confirmPassError = true;
      setConfirmPasswordError(true);
    }

    if (emailError || passError || confirmPassError) return;

    setShowProgress(true);
    axios.post(`${ServerUrl}/api/Auth/register`, {username, password}, {
      'Content-Type': 'application/json;charset=UTF-8',
    }).then(res => {
      dispatch({type:'SET_MESSAGE', data: {title: 'Success', lines: ["You are registered successfully.", "An email has been sent to your email. Please check your email, and activate your account by clicking the link in your email."]}})
    }).catch(() => {
      dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: ["Fail to create your account."]}})
    }).finally(() => {
      setShowProgress(false);
    });
  }

    return (
      <div style={{minHeight: '600px'}}>
      <Card title="Create Account">
        <div style={{fontSize: '18px'}}>Email</div>
        <TextField style={{ width: '100%'}}
            size="small"
            margin="dense"
            type="text"
            fullWidth
            variant="outlined"
            value={username}
            onChange={(e) => {setUsername(e.target.value); setEmailError(false)}}
        />
        {emailError && <div style={{fontSize: '14px', color: 'red'}}>Invalid email</div>}

        <div style={{fontSize: '18px', marginTop: '20px'}}>Password</div>
        <TextField  style={{ width: '100%'}}
            size="small"
            margin="dense"
            type={showPassword ? "text" : "password"}
            fullWidth
            variant="outlined"
            value={password}
            onChange={(e) => {setPassword(e.target.value); setPasswordError(false)}}
            InputProps={{ 
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={() => setShowPassword(!showPassword)}
                  >
                    {showPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              )
            }}
        />
        {passwordError && <div style={{fontSize: '14px', color: 'red'}}>Password must have at leat one lower case character, one upper case character and one number and length between 6 and 32.</div>}

        <div style={{fontSize: '18px', marginTop: '20px'}}>Confirm Password</div>
        <TextField  style={{ width: '100%'}}
            size="small"
            margin="dense"
            type={showConfirmPassword ? "text" : "password"}
            fullWidth
            variant="outlined"
            value={confirmPassword}
            onChange={(e) => { setConfirmPassword(e.target.value); setConfirmPasswordError(false)}}
            InputProps={{ 
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                  >
                    {showConfirmPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              )
            }}
        />
        {confirmPasswordError && <div style={{fontSize: '14px', color: 'red'}}>Password not match</div>}

        <div style={{display:'flex', justifyContent:'center', margin:'20px 10px 20px 10px'}}>
          <Button showIcon={showProgress} label="Create Account" onClick={onCreateAccount} 
            style={{color: 'white', backgroundColor:'#1976D3', width: '220px', padding: '12px 20px', margin: 'auto'}} />
        </div>

      </Card>
      </div>
    );
}

export default SignUp;

