import React, { useState, useContext } from 'react';
import TextField from '@mui/material/TextField';
import axios from 'axios';
import { ServerUrl } from '../../../../config';
import { AppContext } from "../../../../context/appContext";
import Dialog from '@mui/material/Dialog';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import DialogTitle from '@mui/material/DialogTitle';
import Button from '@mui/material/Button';
import RotationProgress from '../../../../components/rotation/rotationProgress';
import { StyledAddButton, StyledCard } from '../../../../components/styled';

const CreateNewGroup = ({onCreated}) => {
  const { CurrentUser } = useContext(AppContext);
  const [name, setName] = useState('');
  const [messageTitle, setMessageTitle] = useState('Error');
  const [messageText, setMessageText] = useState(null);
  const [working, setWorking] = useState(false);

  const onAddGroup = () => {
    if (name.length === 0) return;
    
    setWorking(true);
    axios.post(`${ServerUrl}/api/WebContentGroup`, {name}, { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }, 'Content-Type': 'application/json;charset=UTF-8'})
      .then((res) => {        
        setWorking(false);
        onCreated();
    }).catch(function (error) {
      setMessageTitle("Error");
      if (error.response) {
        setMessageText(error.response.data);
      } else {
        setMessageText("Fail to add to the group");
      }
      setWorking(false);
    });;
  }

  return (
    <StyledCard>
        <div style={{fontSize: '16px'}}>Name</div>
        <div style={{display: 'flex', alignItems: 'center', gap: '10px'}}>
          <TextField style={{ width: '100%', marginBottom: '8px', backgroundColor: 'white'}}
                size="small"
                margin="dense"
                type="text"
                placeholder='Enter a new group name'
                fullWidth
                variant="outlined"
                value={name}
                onChange={(e) => setName(e.target.value)}
          />
          <StyledAddButton disabled={name.length === 0} onClick={onAddGroup}>
            {working && <span style={{marginRight: '10px'}}><RotationProgress speed="1s" value={75} size={20} /></span>}
            <span>Add</span>
          </StyledAddButton>
        </div>
        <Dialog maxWidth={'sm'} fullWidth={true} open={messageText !== null}>
            <DialogTitle>
                {messageTitle}
            </DialogTitle>
            <DialogContent dividers>
                {messageText}
            </DialogContent>
            <DialogActions>
                <Button onClick={() => setMessageText(null)}>Close</Button>
            </DialogActions>
        </Dialog>
    </StyledCard>
  )
}

export default CreateNewGroup;
