import React, { useContext } from "react";
import { Link } from "react-router-dom";
import styled from 'styled-components';
import MenuItems from "./menuItems";
import { menuItems, userMenus, siginMenu } from "./menus";
import { AppContext } from '../../context/appContext';

const StyledMenuItems = styled.div`
  position: relative;
  padding: 16px 20px 16px 16px;
  font-size: 16px;
  font-size: 16px;
  cursor: pointer;
  white-space: nowrap;
  color: white;

  &:hover {
    background-color: #04aa6d;
  }
`;

const StyledMenus = styled.div`
	display: none;
  width: 80%;
  margin: auto;
	font-family: sans-serif;
	color: white;
	align-items: center;
	justify-content: space-between;


  @media (min-width: 768px) {
    display: flex;
  }
`;

const NavbarComponent = () => {
	const { CurrentUser } = useContext(AppContext);

	return (
    <StyledMenus>
      <div style={{display: 'flex'}}>
        <Link style={{ textDecoration: 'none', color: 'white' }} to={'/'}>
          <StyledMenuItems className="menu-items">
                <span style={{marginRight: '8px'}}>Home</span>
          </StyledMenuItems>
        </Link>
        {menuItems.map((menu, index) => {
          const depthLevel = 0;
          return <MenuItems items={menu} key={index} depthLevel={depthLevel} rightSide={false} />;
        })}
      </div>
      { !CurrentUser &&
        <div style={{display: 'flex'}}>
          {siginMenu.map((menu, index) => {
            const depthLevel = 0;
            return <MenuItems items={menu} key={index} depthLevel={depthLevel} rightSide={true} />;
          })}
        </div>
      }
      { CurrentUser &&
        <div style={{display: 'flex'}}>
          {userMenus.map((menu, index) => {
            const depthLevel = 0;
            return <MenuItems items={menu} key={index} depthLevel={depthLevel} rightSide={true} />;
          })}
        </div>
      }
    </StyledMenus>
	)
}

export default NavbarComponent;