import React from 'react';
import CheckIcon from '@mui/icons-material/Check';

const CheckBox = ({checked, onChanged}) => {
  return (
    <div style={{width: '20px', height: '20px', border: '1px solid gray', borderRadius: '2px', backgroundColor: 'white'}} onClick={onChanged}>
      {checked && <CheckIcon style={{width: '20px', height: '20px'}}/>}
    </div>
  )
}

export default CheckBox;