import React, { useEffect } from 'react';
import { useParams } from 'react-router-dom';
import ProjectPage from './pages/project';
import CoordinatePage from './pages/coordinate';
import WellborePage from './pages/wellbore';
import BoundingboxPage from './pages/boundingbox';
import GeophonePage from './pages/geophone';
import PerforatorPage from './pages/perforator';
import SeismicEventPage from './pages/seismicEvent';
import FormationPage from './pages/formation';
import HistogramPage from './pages/histogram';
import NavigationPage from './pages/navigation';
import ObjectInfoPage from './pages/objectInfo';
import MeasurementPage from './pages/measurement';

const DocTopic = () => {
  const { topic } = useParams();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  if (topic === "project") {
    return <ProjectPage />
  }
  else if (topic === "coordinate") {
    return <CoordinatePage />
  }
  else if (topic === "wellbore") {
    return <WellborePage />
  }
  else if (topic === "boundingbox") {
    return <BoundingboxPage />
  }
  else if (topic === "geophone") {
    return <GeophonePage />
  }
  else if (topic === "perforator") {
    return <PerforatorPage />
  }
  else if (topic === "seismicEvent") {
    return <SeismicEventPage />
  }
  else if (topic === "formation") {
    return <FormationPage />
  }
  else if (topic === "histogram") {
    return <HistogramPage />
  }
  else if (topic === "navigation") {
    return <NavigationPage />
  }
  else if (topic === "objectInfo") {
    return <ObjectInfoPage />
  }
  else if (topic === "measurement") {
    return <MeasurementPage />
  } else {
    return null;
  }
}

export default DocTopic;
