import React, { useState, useContext } from 'react';
import styled from 'styled-components';
import axios from 'axios';
import { AppContext } from '../../../context/appContext';
import { ServerUrl } from '../../../config';
import DialogContainer from '../../../components/dialog/dialogContainer';
import TextField from '@mui/material/TextField';
import { useNavigate } from 'react-router-dom';
import { StyledTableCellBold, StyledTableCell, StyledTableRow } from '../../../components/styled/index';
import DeleteIcon from '@mui/icons-material/Delete';
import PeopleIcon from '@mui/icons-material/People';
import AccountTreeIcon from '@mui/icons-material/AccountTree';
import EditIcon from '@mui/icons-material/Edit';
import Tooltip from '@mui/material/Tooltip';

const StyledDiv = styled.div`
  padding: 6px;
  cursor: pointer;
  &:hover {
    background-color: white;
  }
`;


const TeamRow = ({item, onTaskDone}) => {
  const navigate = useNavigate();
  const { CurrentUser, dispatch } = useContext(AppContext);
  
  const [message, setMessage] = useState(null);
  const [editMessage, setEditMessage] = useState(null);
  const [deleteMessage, setDeleteMessage] = useState(null);
  const [editName, setEditName] = useState(item.name);
  const [editDescription, setEditDescription] = useState(item.description);
  const [isOver, setIsOver] = useState(false);

  const onEditTeam = () => {
    if (!CurrentUser || !CurrentUser.token) return;

    if (editName === item.name) return;
    
    axios.put(`${ServerUrl}/api/teams/${item.id}`, {name: editName, description: editDescription}, { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }, 'Content-Type': 'application/json;charset=UTF-8'})
    .then((res) => {
        onTaskDone();
      }).catch((err) => {
        alert(err.response.data)
        dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: [err.response.data]}})
        // setMessage({title: 'Error', text: `Fail to edit group ${item.name}`})
      }
    );  
  }

  const onRemoveTeam = () => {
    if (!CurrentUser || !CurrentUser.token) return;
    
    axios.delete(`${ServerUrl}/api/teams/${item.id}`, { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }})
    .then((res) => {
        onTaskDone();
      }).catch((err) => {
        dispatch({type:'SET_MESSAGE', data: {title: 'Error', lines: [err.response.data]}})
      }
    );  
  }

  return (
    <StyledTableRow onMouseEnter={() => setIsOver(true)} onMouseLeave={() => setIsOver(false)}>
        <StyledTableCellBold>
         {item.name}
        </StyledTableCellBold>
        <StyledTableCell>
          <span>{item.description}</span>
        </StyledTableCell>
        <div style={{display: 'flex', width: '180px'}}>
          {isOver &&
            <>
              <Tooltip title="Edit team">
                <StyledDiv style={{marginRight: '10px'}} onClick={()=> setEditMessage({title: 'Edit Team'})}>
                  <EditIcon style={{width: '24px', height: '24px', color: '#1976d2'}} />
                </StyledDiv>
              </Tooltip>
              <Tooltip title="Manage members">
                <StyledDiv style={{marginRight: '10px'}}>
                  <PeopleIcon style={{width: '24px', height: '24px', color: '#1976d2'}} onClick={()=> navigate(`/teams/${item.id}/manage`)} />
                </StyledDiv>
              </Tooltip>
              <Tooltip title="View contents">
                <StyledDiv style={{marginRight: '10px'}}>
                  <AccountTreeIcon style={{width: '24px', height: '24px', color: '#1976d2'}} onClick={()=> navigate(`/teams/${item.id}/webcontents`)} />
                </StyledDiv>
              </Tooltip>
              <Tooltip title="Remove team">
                <StyledDiv>
                  <DeleteIcon style={{width: '24px', height: '24px', color: '#C60001'}} onClick={()=> setDeleteMessage({title: 'Remove Team'})} />
                </StyledDiv>
              </Tooltip>
            </>
          }
        </div>

      <DialogContainer message={editMessage} onCancel={() => setEditMessage(null)} onOk={() => {setEditMessage(null); onEditTeam()}} >
        <div>Name</div>
        <TextField style={{ width: '100%', marginBottom: '8px', backgroundColor: 'white'}}
                size="small"
                margin="dense"
                type="text"
                fullWidth
                variant="outlined"
                value={editName}
                onChange={(e) => setEditName(e.target.value)}
        />
        <div style={{fontSize: '16px'}}>Description</div>
          <TextField  style={{ width: '100%', marginBottom: '16px'}}
              size="small"
              margin="dense"
              fullWidth
              variant="outlined"
              value={editDescription}
              onChange={(e) => setEditDescription(e.target.value)}
        />
      </DialogContainer>
      <DialogContainer message={deleteMessage} onCancel={() => setDeleteMessage(null)} onOk={() => {setDeleteMessage(null); onRemoveTeam()}} >
        <div>Do you want to remove this team?</div>
        <h3>{item.name}</h3>
      </DialogContainer>
      <DialogContainer message={message} onClose={() => setMessage(null)} >
        <div>Fail to remove team {item.name}</div>
      </DialogContainer>
    </StyledTableRow>
  )
}

export default TeamRow;