import React, { useState } from 'react';
import TextField from '@mui/material/TextField';
import { StyledAddButton, StyledCard } from '../../../components/styled';

const CreateNew =({onCreate}) => {
  const [name, setName] = useState('');
  const [description, setDescription] = useState('');

  const onAddPage = () => {
    if (name.length === 0) return;
    onCreate({name, description})
  } 

  return (
    <StyledCard style={{fontSize: '14px'}}>
        <div style={{fontSize: '16px'}}>Name</div>
        <TextField style={{ width: '100%', marginBottom: '16px'}}
              size="small"
              margin="dense"
              type="text"
              fullWidth
              variant="outlined"
              value={name}
              onChange={(e) => setName(e.target.value.trim())}
        />
        <div style={{fontSize: '16px'}}>Description</div>
        <TextField  style={{ width: '100%', marginBottom: '16px'}}
            size="small"
            margin="dense"
            fullWidth
            variant="outlined"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
        />
        <div style={{display: 'flex'}}>

        </div>
        <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', flexDirection: 'row-reverse'}}>
          <StyledAddButton disabled={name.length === 0} onClick={onAddPage}>
            <span>Add</span>
          </StyledAddButton>
        </div>
    </StyledCard>
  )
}

export default CreateNew;
