import React from 'react';
import styled from 'styled-components';
import AddIcon from '@mui/icons-material/Add';

export const StyledButton = styled.div`
 color: white;
 background-color: ${props => { 
  if (props.disabled) {
    return "gray";
  }
  return props.backColor || '#04AA6D'} };
 font-size: 16px;
 font-weight: normal;
 font-family: 'Source Sans Pro',sans-serif;
 border-radius: 6px;
 padding: 0px 16px 0px 8px;
 display: flex;
 height: 36px;
 align-items: center;
 cursor: ${props => props.disabled ? "not-allowed" : "pointer" };

 &:hover {
  background-color: ${props => { 
    if (props.disabled) {
      return "gray";
    }
    return props.backOverColor || '#059862'}};
 }
`;

export const StyledIcon = styled.div`
  display: inline-block;
  padding-top: 4px;
  margin-right: 4px;
  transition-duration: 0.5s;
  transform-origin: center center;
  transform: ${props => props.isOpen ? "rotate(45deg)" : "rotate(0deg)"};
`;

const CreateNewButton = ({title, showCreate, onClick, disabled}) => {
  return (
    <StyledButton disabled={disabled} onClick={disabled ? undefined : onClick}>
      <StyledIcon isOpen={showCreate}>
        <AddIcon style={{width: '24px', height: '24px', color: 'white'}} /> 
      </StyledIcon>
      <span style={{marginLeft: '0px'}}>{title}</span>
    </StyledButton>
  )
}

export default CreateNewButton;
