import React, { useState, useContext } from 'react';
import styled from 'styled-components';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import axios from 'axios';
import { AppContext } from "../../../../context/appContext";
import { ServerUrl } from '../../../../config';
import DialogContainer from '../../../../components/dialog/dialogContainer';
import TextField from '@mui/material/TextField';
import { StyledTableRow } from '../../../../components/styled';

const StyledDiv = styled.div`
  padding: 6px;
  &:hover {
    background-color: white;
  }
`;


const GroupRow = ({item, onTaskDone}) => {
  const { CurrentUser } = useContext(AppContext);
  
  const [isOver, setIsOver] = useState(false);
  const [message, setMessage] = useState(null);
  const [editMessage, setEditMessage] = useState(null);
  const [deleteMessage, setDeleteMessage] = useState(null);
  const [editName, setEditName] = useState(item.name);

  const onEditGroup = () => {
    if (!CurrentUser || !CurrentUser.token) return;

    if (editName === item.name) return;
    
    axios.put(`${ServerUrl}/api/WebContentGroup/${item.id}`, {name: editName}, { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }, 'Content-Type': 'application/json;charset=UTF-8'})
    .then((res) => {
        onTaskDone();
      }).catch(function () {
        setMessage({title: 'Error', text: `Fail to edit group ${item.name}`})
      }
    );  
  }

  const onDeleteGroup = () => {
    if (!CurrentUser || !CurrentUser.token) return;
    
    axios.delete(`${ServerUrl}/api/WebContentGroup/${item.id}`, { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }})
    .then((res) => {
        onTaskDone();
      }).catch((err) => {
        setMessage({title: `Error`, text: err.response.data})
      }
    );  
  }

  return (
    <StyledTableRow onMouseEnter={() => setIsOver(true)} onMouseLeave={() => setIsOver(false)}>
      <div style={{fontSize: '14px'}}>{item.name}</div>
      {isOver && 
        <div style={{display: 'flex'}}>
          <StyledDiv style={{marginRight: '10px'}}>
            <EditIcon style={{width: '20px', height: '20px', color: 'black'}} onClick={()=> setEditMessage({title: 'Edit Group'})} />
          </StyledDiv>
          <StyledDiv>
            <DeleteIcon style={{width: '20px', height: '20px', color: 'black'}} onClick={()=> setDeleteMessage({title: 'Delete Group'})} />
          </StyledDiv>
        </div>
      }

      <DialogContainer message={editMessage} onCancel={() => setEditMessage(null)} onOk={() => {setEditMessage(null); onEditGroup()}} >
        <div>Enter a new group name</div>
        <TextField style={{ width: '100%', marginBottom: '8px', backgroundColor: 'white'}}
                size="small"
                margin="dense"
                type="text"
                fullWidth
                variant="outlined"
                value={editName}
                onChange={(e) => setEditName(e.target.value)}
          />
      </DialogContainer>
      <DialogContainer message={deleteMessage} onCancel={() => setDeleteMessage(null)} onOk={() => {setDeleteMessage(null); onDeleteGroup()}} >
        <div>Do you want to delete this group?</div>
        <h3>{item.name}</h3>
      </DialogContainer>
      <DialogContainer message={message} onClose={() => setMessage(null)} >
        {message && <div>{message.text}</div>}
      </DialogContainer>
    </StyledTableRow>
  )
}

export default GroupRow;