import React, { useState } from "react";
import { StyledButton, StyledHover } from "../styled";
import Popover from '@mui/material/Popover';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import ClearIcon from '@mui/icons-material/Clear';
import CheckIcon from '@mui/icons-material/Check';

const Options = ({name, description, options, selected, onOptionSelected}) => {
  const [anchorEl, setAnchorEl] = useState(null);

  const open = Boolean(anchorEl);
  const id = open ? 'simple-popover' : undefined;

  return (
    <div style={{marginRight: '20px'}}>
      <StyledButton backColor="#F6F8FA" backOverColor="#f2f5f8"
        style={{color: 'black', border: '1px solid #a6a8aa', borderRadius: '6px', fontSize: '14px', fontWeight: '600', padding: '6px 10px'}}
        onClick={(e) => setAnchorEl(e.currentTarget) }>
        {name} <ArrowDropDownIcon style={{width: '24px', height: '24px'}} />
      </StyledButton>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={() => setAnchorEl(null)}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        style={{marginTop: '10px'}}
      >
        <div style={{padding: '10px', minWidth: '200px', display: 'flex', borderBottom: '1px solid #e2e5e8', justifyContent: 'space-between', alignItems: 'center'}}>
          <span>{description}</span>
          <StyledHover style={{cursor: 'pointer'}} onClick={() => setAnchorEl(null)}>
            <ClearIcon style={{width: '20px', height: '20px'}} />
          </StyledHover>
        </div>

        <div style={{maxHeight: '300px', overflowY: 'auto', overflowX: 'hidden'}}>
          {options && options.map((item) => {
            return (
              <StyledHover key={item.id} style={{padding: '10px', minWidth: '200px', display: 'flex', borderBottom: '1px solid #e2e5e8', cursor: 'pointer'}} 
                onClick={() => { setAnchorEl(null); onOptionSelected(item.id) }}>
                <div style={{width: '30px'}}>
                  {selected === item.id && <CheckIcon style={{width: '20px', height: '20px'}} />}
                </div>
                <div>{item.name}</div>
              </StyledHover>
            )
          })}
        </div>
      </Popover>
    </div>
  )

}

export default Options;