import React, { useState, useContext } from 'react';
import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import axios from 'axios';
import { AppContext } from '../../../../context/appContext';
import { ServerUrl } from '../../../../config';

const CopyDialog = ({open, data, teamId, onCancel, onOk, width='sm'}) => {
  const [newName, setNewName] = useState(data.name + '-copy');
  const [copyError, setCopyError] = useState(false);
  const { CurrentUser } = useContext(AppContext);

  const onCopy = () => {
    if (newName === '') return;

    axios.post(`${ServerUrl}/api/TeamContent/copy`, {teamId: parseInt(teamId), pageId: data.id, name: newName},
      { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }}
      ).then(res => {
        onOk();
      }).catch((err) => {
        setCopyError(true);
      });
  }

  return (
    <Dialog maxWidth={width} fullWidth={true} open={open}>
        <DialogTitle>Copy Content</DialogTitle>
        <DialogContent dividers>
          {copyError && <div style={{color: 'red'}}>Fail to copy web content</div>}
          <div style={{display: 'flex', alignItems: 'center', gap: '10px'}}>
            <div style={{width: '40px'}}>Name</div>
            <TextField style={{ flex: '1', marginBottom: '8px', backgroundColor: 'white', fontSize: '14px'}}
                    size="small"
                    margin="dense"
                    type="text"
                    fullWidth
                    multiline
                    variant="outlined"
                    value={newName}
                    onChange={(e) => {
                      setCopyError(false);
                      setNewName(e.target.value)
                    }}
            />
          </div>
          <div style={{height: '30px'}}>
          {newName === '' && <div style={{color: 'red', marginLeft: '50px'}}>Name is required</div>}                      
          </div>
        </DialogContent>
        <DialogActions>
          {onCancel && <Button onClick={onCancel}>Cancel</Button>}
          {onOk && <Button onClick={onCopy} disabled={newName === ''}>Ok</Button>}
        </DialogActions>
    </Dialog>
  )
}
  
export default CopyDialog;
