import React, { useState, useContext } from 'react';
import styled from 'styled-components';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import PublishIcon from '@mui/icons-material/Publish';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import axios from 'axios';
import { AppContext } from '../../../../context/appContext';
import { ServerUrl } from '../../../../config';
import DialogContainer from '../../../../components/dialog/dialogContainer';
import { useNavigate } from 'react-router-dom';
import { StyledTableRow, StyledTableCellBold, StyledTableCell } from '../../../../components/styled';
import Tooltip from '@mui/material/Tooltip';
import PublishPage from './publishpage';
import { Collapse } from '@mui/material';
import CopyDialog from './copypage';

const StyledDiv = styled.div`
  padding: 6px;
  cursor: pointer;
  &:hover {
    background-color: white;
  }
`;

const GroupRow = ({item, onTaskDone}) => {
  const navigate = useNavigate();
  const { CurrentUser } = useContext(AppContext);
  
  const [isOver, setIsOver] = useState(false);
  const [message, setMessage] = useState(null);
  const [copyOpen, setCopyOpen] = useState(false);
  const [deleteMessage, setDeleteMessage] = useState(null);
  const [publishMessage, setPublishMessage] = useState(null);
  const [publishOpen, setPublishOpen] = useState(false);

  const onDeleteWebpage = () => {
    if (!CurrentUser || !CurrentUser.token) return;
    
    axios.delete(`${ServerUrl}/api/WebContent/${item.id}`, { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }})
    .then((res) => {
        onTaskDone();
      }).catch(function () {
        setMessage({title: 'Error', text: `Fail to delete webpage ${item.name}`})
      }
    );  
  }

  return (
    <>
      <StyledTableRow onMouseEnter={() => setIsOver(true)} onMouseLeave={() => setIsOver(false)}>
        <StyledTableCellBold>
          <span style={{cursor: 'pointer'}} onClick={()=> navigate(`/webcontents/${item.id}`)}>{item.name}</span>
        </StyledTableCellBold>
        <StyledTableCell>
          {item.group}
        </StyledTableCell>
        <StyledTableCell>
          <span>{new Date(item.lastUpdate).toLocaleDateString()}</span>
        </StyledTableCell>
        <StyledTableCell>
          <span>{item.description}</span>
        </StyledTableCell>
        <StyledTableCell>
          <span>{item.isPublic ? "Yes" : ""}</span>
        </StyledTableCell>
        <div style={{display: 'flex', width: '140px'}}>
          {isOver &&
            <>
              <Tooltip title="Edit content">
                <StyledDiv>
                  <EditIcon style={{width: '24px', height: '20px', color: '#1976d2'}} onClick={()=> navigate(`/webcontents/${item.id}`)} />
                </StyledDiv>
              </Tooltip>
              <Tooltip title="Copy content">
                <StyledDiv>
                  <ContentCopyIcon style={{width: '24px', height: '20px', color: '#1976d2'}} onClick={()=> setCopyOpen(true)} />
                </StyledDiv>
              </Tooltip>
              <Tooltip title="Publish content">
                <StyledDiv>
                  <PublishIcon style={{width: '24px', height: '20px', color: '#1976d2'}} onClick={()=> setPublishMessage({title: 'Publish Content'})} />
                </StyledDiv>
              </Tooltip>
              <Tooltip title="Remove content">
                <StyledDiv>
                  <DeleteIcon style={{width: '24px', height: '20px', color: '#C60001'}} onClick={()=> setDeleteMessage({title: 'Delete Content'})} />
                </StyledDiv>
              </Tooltip>
            </>
          }
        </div>

        <CopyDialog open={copyOpen} data={item} onCancel={() => setCopyOpen(false)} onOk={() => window.location.reload()} />
        <DialogContainer message={deleteMessage} onCancel={() => setDeleteMessage(null)} onOk={() => {setDeleteMessage(null); onDeleteWebpage()}} >
          <div>Do you want to delete this webpage?</div>
          <h3>{item.name}</h3>
        </DialogContainer>
        <DialogContainer width={'md'} message={publishMessage} onClose={() => setPublishMessage(null)} >
          <PublishPage data={item} />
        </DialogContainer>
        <DialogContainer message={message} onClose={() => setMessage(null)} >
          <div>Fail to delete webpage {item.name}</div>
        </DialogContainer>
      </StyledTableRow>
      <Collapse in={publishOpen}>
          <PublishPage data={item} />
      </Collapse>
    </>
  )
}

export default GroupRow;