import React, { useContext, useEffect } from 'react';
import { AppContext } from "../../context/appContext";
import PeopleIcon from '@mui/icons-material/People';
import AccountTreeIcon from '@mui/icons-material/AccountTree';
import PasswordIcon from '@mui/icons-material/Password';
import LogoutIcon from '@mui/icons-material/Logout';
import { useNavigate } from 'react-router-dom';
import { StyledBox, StyledBoxContainer, StyledPageTitle } from '../../components/styled/index';
import WithPosition from '../../components/common/hoc/resetPagePosition';

const MyHome = () => {
  const { CurrentUser, dispatch } = useContext(AppContext);
  const navigate = useNavigate();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div style={{minHeight: '800px', width: '80%', margin: 'auto'}}>
      <div style={{padding: '20px'}}>
        <StyledPageTitle>Welcome {CurrentUser.username}</StyledPageTitle>
        <StyledBoxContainer>
          <StyledBox onClick={() => navigate('/teams')}>
            <PeopleIcon style={{width: '60px', height: '60px', color: '#008B8B'}} />
            <span style={{fontSize: '16px', fontWeight: 'bold'}}>Your Teams</span>
          </StyledBox>
          <StyledBox onClick={() => navigate('/webcontents')}>
            <AccountTreeIcon style={{width: '60px', height: '60px', color: '#008B8B'}} />
            <span style={{fontSize: '16px', fontWeight: 'bold'}}>Your Web Contents</span>
          </StyledBox>
          <StyledBox onClick={() => navigate('/changepassword')}>
            <PasswordIcon style={{width: '60px', height: '60px', color: '#008B8B'}} />
            <span style={{fontSize: '16px', fontWeight: 'bold'}}>Change Password</span>
          </StyledBox>
          <StyledBox onClick={() => {dispatch({type:'LOGOUT'}); navigate('/')}}>
            <LogoutIcon style={{width: '60px', height: '60px', color: '#008B8B'}} />
            <span style={{fontSize: '16px', fontWeight: 'bold'}}>Sign Out</span>
          </StyledBox>
        </StyledBoxContainer>
      </div>
    </div>
  )
}

export default WithPosition(MyHome);
