import React, { useState, useContext } from 'react';
import styled from 'styled-components';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import PublishIcon from '@mui/icons-material/Publish';
import axios from 'axios';
import { AppContext } from '../../../../context/appContext';
import { ServerUrl } from '../../../../config';
import DialogContainer from '../../../../components/dialog/dialogContainer';
import TextField from '@mui/material/TextField';
import { useNavigate } from 'react-router-dom';
import { StyledTableRow, StyledTableCellBold, StyledTableCell } from '../../../../components/styled';
import Tooltip from '@mui/material/Tooltip';
import PublishPage from './publishpage';
import { Collapse } from '@mui/material';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import CopyDialog from './copypage';

const StyledDiv = styled.div`
  padding: 6px;
  cursor: pointer;
  &:hover {
    background-color: white;
  }
`;

const GroupRow = ({teamId, item, onTaskDone}) => {
  const navigate = useNavigate();
  const { CurrentUser } = useContext(AppContext);
  
  const [isOver, setIsOver] = useState(false);
  const [message, setMessage] = useState(null);
  const [editMessage, setEditMessage] = useState(null);
  const [deleteMessage, setDeleteMessage] = useState(null);
  const [editName, setEditName] = useState(item.name);
  const [publishOpen, setPublishOpen] = useState(false);
  const [copyOpen, setCopyOpen] = useState(false);

  const onEditGroup = () => {
    if (!CurrentUser || !CurrentUser.token) return;

    if (editName === item.name) return;
    
    axios.put(`${ServerUrl}/api/TeamContentGroup/${item.id}`, {name: editName}, { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }, 'Content-Type': 'application/json;charset=UTF-8'})
    .then((res) => {
        onTaskDone();
      }).catch(function () {
        setMessage({title: 'Error', text: `Fail to edit group ${item.name}`})
      }
    );  
  }

  const onDeleteWebpage = () => {
    if (!CurrentUser || !CurrentUser.token) return;
    
    axios.delete(`${ServerUrl}/api/TeamContent/${teamId}/${item.id}`, { headers: { 'Authorization' : 'Bearer ' + CurrentUser.token }})
    .then((res) => {
        onTaskDone();
      }).catch(function () {
        setMessage({title: 'Error', text: `Fail to delete webpage ${item.name}`})
      }
    );  
  }

  return (
    <>
    <StyledTableRow onMouseEnter={() => setIsOver(true)} onMouseLeave={() => setIsOver(false)}>
        <StyledTableCellBold style={{cursor: 'pointer'}} onClick={()=> navigate(`/teams/${teamId}/webcontents/${item.id}`)}>
         {item.name}
        </StyledTableCellBold>
        <StyledTableCell>
          {item.group}
        </StyledTableCell>
        <StyledTableCell>
          <span>{new Date(item.lastUpdate).toLocaleDateString()}</span>
        </StyledTableCell>
        <StyledTableCell>
          <span>{item.description}</span>
        </StyledTableCell>

        <div style={{display: 'flex', width: '140px'}}>
          {isOver &&
            <>
              <Tooltip title="Edit Content">
                <StyledDiv style={{marginRight: '10px'}}>
                  <EditIcon style={{width: '24px', height: '24px', color: '#1976d2'}} onClick={()=> navigate(`/teams/${teamId}/webcontents/${item.id}`)} />
                </StyledDiv>
              </Tooltip>
              <Tooltip title="Copy content">
                <StyledDiv>
                  <ContentCopyIcon style={{width: '24px', height: '20px', color: '#1976d2'}} onClick={()=> setCopyOpen(true)} />
                </StyledDiv>
              </Tooltip>
              <Tooltip title="Publish content">
                <StyledDiv>
                  <PublishIcon style={{width: '24px', height: '20px', color: '#1976d2'}} onClick={()=> setPublishOpen(!publishOpen)} />
                </StyledDiv>
              </Tooltip>
              <Tooltip title="Delete content">
                <StyledDiv>
                  <DeleteIcon style={{width: '24px', height: '24px', color: '#C60001'}} onClick={()=> setDeleteMessage({title: 'Delete Content'})} />
                </StyledDiv>
              </Tooltip>
            </>
          }
        </div>

      <CopyDialog open={copyOpen} data={item} teamId={teamId} onCancel={() => setCopyOpen(false)} onOk={() => window.location.reload()} />
      <DialogContainer message={editMessage} onCancel={() => setEditMessage(null)} onOk={() => {setEditMessage(null); onEditGroup()}} >
        <div>Enter a new group name</div>
        <TextField style={{ width: '100%', marginBottom: '8px', backgroundColor: 'white'}}
                size="small"
                margin="dense"
                type="text"
                fullWidth
                variant="outlined"
                value={editName}
                onChange={(e) => setEditName(e.target.value)}
        />
      </DialogContainer>
      <DialogContainer message={deleteMessage} onCancel={() => setDeleteMessage(null)} onOk={() => {setDeleteMessage(null); onDeleteWebpage()}} >
        <div>Do you want to delete this web content?</div>
        <h3>{item.name}</h3>
      </DialogContainer>
      <DialogContainer message={message} onClose={() => setMessage(null)} >
        <div>Fail to delete web content {item.name}</div>
      </DialogContainer>
    </StyledTableRow>
    <Collapse in={publishOpen}>
      <PublishPage data={item} teamId={teamId} />
    </Collapse>
    </>
  )
}

export default GroupRow;