import React from "react";
import { useLocation } from "react-router-dom";

const Footer = (props) => {
  const location = useLocation();

  if (location.pathname.includes("/webcontents/")) return null;

    return (
      <div style={{width:"100%", padding: "100px 0px", backgroundColor: "#282A35", color:'#FFFFFF'}}>
        <div style={{display: 'flex', justifyContent: 'center', marginBottom: '20px'}}>
          <span>© {(new Date()).getFullYear()} Copyright by W3Element</span>
        </div>           
        <div style={{display: 'flex', justifyContent: 'center'}}>
          <span>Email: support@w3element.com</span>
        </div> 
      </div>
    );
}

export default Footer;

