import React from 'react';
import styled from 'styled-components';

const StyledDiv = styled.div`
  padding: 2px;
  &:hover {
    background-color: white;
  }
`;

const Container = (props) => {
  return (
    <StyledDiv onClick={props.onClick}>
      {props.children}
    </StyledDiv>
  )
}

export default Container;