import React, { useState, useContext, useEffect } from "react";
import Card from "./card";
import axios from 'axios';
import { useSearchParams } from 'react-router-dom';
import Loading from '../../components/common/loading';
import { useNavigate } from 'react-router-dom';
import Button from '../../components/common/buttons/IconRatationButton';
import { ServerUrl } from '../../config';

const ActivateAccount = () => {
  const [error, setError] = useState('');
  const [activating, setActivating] = useState(false);
  const [activated, setActivated] = useState(false);

  let [searchParams] = useSearchParams();
  let paraEmail = searchParams.get("email");
  let paraCode = searchParams.get("code");
 
  const navigate = useNavigate();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  
  useEffect(() => {
    if (paraEmail && paraCode && paraEmail.length > 0 && paraCode.length > 0 && ValidateEmail(paraEmail)) {
      setActivating(true);
      axios.get(`${ServerUrl}/api/Auth/activate?email=${paraEmail}&code=${paraCode}`)
        .then(() => {
          setActivated(true); 
        })
        .catch(() => setError('Fail to activate your account.'))
        .finally(() => setActivating(false))
    }
    else {
        setError('Invalid data.');
    }
  },[paraEmail, paraCode]);

  const ValidateEmail = (email) => {
    return /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(email);
  }
  
  return (
    <div style={{minHeight: '600px'}}>
      <Card title="Activate Account">
        {activating && <Loading text="Activating..." />}
        {activated && (
          <>
            <div>Your account is successfully activated.</div>
            <div style={{display:'flex', justifyContent:'center', margin:'50px 10px 20px 10px'}}>
              <Button label="Sign In" onClick={() => navigate("/signin")}
                style={{color: 'white', backgroundColor:'#1976D3', width: '200px', padding: '12px 20px', margin: 'auto'}} />
            </div>
          </>
        
        )}
        <div style={{marginBottom: '100px'}}>{error}</div>
      </Card>
    </div>
  );
}

export default ActivateAccount;

